.teacher-cup-dialog .co-dialog-content {
  width: 90%;
}

.teacher-cup-dialog .qlMT_top {
  display: block;
  width: 100%;
  min-height: 1.066667rem;
  padding: 0.8rem 0 0.266667rem;
  background-color: #fff;
  color: #404040;
  text-align: center;
  line-height: 0.533333rem;
  white-space: nowrap;
  font-weight: bold;
}

[data-dpr="1"] .teacher-cup-dialog .qlMT_top {
  font-size: 16px;
}

[data-dpr="2"] .teacher-cup-dialog .qlMT_top {
  font-size: 32px;
}

[data-dpr="3"] .teacher-cup-dialog .qlMT_top {
  font-size: 48px;
}

.teacher-cup-dialog .qlMT_content {
  padding: 0.266667rem 0.4rem;
}

[data-dpr="1"] .teacher-cup-dialog .qlMT_content {
  font-size: 14px;
}

[data-dpr="2"] .teacher-cup-dialog .qlMT_content {
  font-size: 28px;
}

[data-dpr="3"] .teacher-cup-dialog .qlMT_content {
  font-size: 42px;
}

.teacher-cup-dialog .qlMT_content .thanks {
  margin-bottom: 0.4rem;
}

.teacher-cup-dialog .qlMT_content .special {
  margin-bottom: 0.2rem;
}

.teacher-cup-dialog .qlMT_content .t-live-list {
  color: #f73657;
  margin-bottom: 0.4rem;
}

.teacher-cup-dialog .qlMT_content li {
  margin-bottom: 0.093333rem;
}

.teacher-cup-dialog .qlMT_content .v-live-tips {
  color: #999;
}

.teacher-cup-dialog .qlMT_content p {
  line-height: 1.4;
}

.teacher-cup-dialog .tlbtn_cancel {
  color: #333;
}

.teacher-cup-dialog .qlMTBottom2 {
  border-top: 1px solid #f0f0f0;
  line-height: 1.2rem;
  height: 1.2rem;
  text-align: center;
}

[data-dpr="1"] .teacher-cup-dialog .qlMTBottom2 {
  font-size: 15px;
}

[data-dpr="2"] .teacher-cup-dialog .qlMTBottom2 {
  font-size: 30px;
}

[data-dpr="3"] .teacher-cup-dialog .qlMTBottom2 {
  font-size: 45px;
}

.teacher-cup-dialog .qlMTBottom2 a {
  display: block;
  width: 100%;
  height: 100%;
}