.ql-editor-h5-wrap {
  position: relative;
}

.ql-editor-h5-wrap .ql-editor-h5 {
  display: block;
  cursor: text;
  font-family: sans-serif;
  overflow: hidden;
  padding: 0.266667rem 0;
}

[data-dpr="1"] .ql-editor-h5-wrap .ql-editor-h5 {
  font-size: 16px;
}

[data-dpr="2"] .ql-editor-h5-wrap .ql-editor-h5 {
  font-size: 32px;
}

[data-dpr="3"] .ql-editor-h5-wrap .ql-editor-h5 {
  font-size: 48px;
}

.ql-editor-h5-wrap .ql-editor-h5 * {
  max-width: 100%;
}

.ql-editor-h5-wrap .ql-editor-h5 p {
  line-height: normal;
  display: block;
}

[data-dpr="1"] .ql-editor-h5-wrap .ql-editor-h5 p {
  margin: 2.5px 0;
}

[data-dpr="2"] .ql-editor-h5-wrap .ql-editor-h5 p {
  margin: 5px 0;
}

[data-dpr="3"] .ql-editor-h5-wrap .ql-editor-h5 p {
  margin: 7.5px 0;
}

.ql-editor-h5-wrap .ql-editor-h5 i,
.ql-editor-h5-wrap .ql-editor-h5 cite,
.ql-editor-h5-wrap .ql-editor-h5 em,
.ql-editor-h5-wrap .ql-editor-h5 var,
.ql-editor-h5-wrap .ql-editor-h5 address,
.ql-editor-h5-wrap .ql-editor-h5 dfn {
  font-style: italic;
}

.ql-editor-h5-wrap .ql-editor-h5 img,
.ql-editor-h5-wrap .ql-editor-h5 iframe {
  width: 100%;
  display: inline-block;
  max-width: 100%;
  vertical-align: middle;
}

.ql-editor-h5-wrap .ql-editor-h5 .list-paddingleft-2 {
  padding-left: 0.8rem;
}

.ql-editor-h5-wrap .ql-editor-h5 .list-paddingleft-1 {
  padding-left: 0;
}

.ql-editor-h5-wrap .ql-editor-h5 .list-paddingleft-3 {
  padding-left: 1.6rem;
}