/**
 * 1px
 */

.co-win-course-eval {
  position: absolute;
  z-index: 200;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  -webkit-text-size-adjust: none;
          text-size-adjust: none;
}

.co-win-course-eval .win {
  position: absolute;
  bottom: 0;
  width: 100%;
  background-color: #fff;
  -webkit-box-sizing: border-box;
     -moz-box-sizing: border-box;
          box-sizing: border-box;
  line-height: 1.4;
  overflow-y: auto;
  max-height: 100%;
  -webkit-border-top-left-radius: 0.266667rem;
          border-top-left-radius: 0.266667rem;
  -webkit-border-top-right-radius: 0.266667rem;
          border-top-right-radius: 0.266667rem;
}

.co-win-course-eval .win .win-title {
  color: #333;
  font-weight: bolder;
  text-align: center;
  padding: 0.426667rem 0;
}

[data-dpr="1"] .co-win-course-eval .win .win-title {
  font-size: 16px;
}

[data-dpr="2"] .co-win-course-eval .win .win-title {
  font-size: 32px;
}

[data-dpr="3"] .co-win-course-eval .win .win-title {
  font-size: 48px;
}

.co-win-course-eval .win .course-title {
  position: relative;
  color: #666;
  padding-bottom: 0.426667rem;
  margin-bottom: 0.533333rem;
}

[data-dpr="1"] .co-win-course-eval .win .course-title {
  font-size: 14px;
}

[data-dpr="2"] .co-win-course-eval .win .course-title {
  font-size: 28px;
}

[data-dpr="3"] .co-win-course-eval .win .course-title {
  font-size: 42px;
}

.co-win-course-eval .win .course-title::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  -webkit-transform-origin: 0 100%;
     -moz-transform-origin: 0 100%;
          transform-origin: 0 100%;
}

[data-dpr="1"] .co-win-course-eval .win .course-title::after {
  height: 0.5px;
}

[data-dpr="2"] .co-win-course-eval .win .course-title::after {
  height: 1px;
}

[data-dpr="3"] .co-win-course-eval .win .course-title::after {
  height: 1.5px;
}

@media (-webkit-min-device-pixel-ratio: 3.1) {
  .co-win-course-eval .win .course-title::after {
    -webkit-transform: scaleY(0.3);
       -moz-transform: scaleY(0.3);
            transform: scaleY(0.3);
  }
}

.co-win-course-eval .win .course-title p {
  padding: 0 0.533333rem;
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.co-win-course-eval .win .score-star {
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
     -moz-box-pack: justify;
          justify-content: space-between;
  padding: 0 0.533333rem;
}

[data-dpr="1"] .co-win-course-eval .win .score-star {
  font-size: 24px;
}

[data-dpr="2"] .co-win-course-eval .win .score-star {
  font-size: 48px;
}

[data-dpr="3"] .co-win-course-eval .win .score-star {
  font-size: 72px;
}

.co-win-course-eval .win .score-star .btn-publish {
  -webkit-border-radius: 0.693333rem;
          border-radius: 0.693333rem;
  font-weight: bolder;
  color: #fff;
  text-align: center;
  padding: 0 1.4em;
  background-color: #f73657;
}

[data-dpr="1"] .co-win-course-eval .win .score-star .btn-publish {
  line-height: 26px;
  font-size: 14px;
}

[data-dpr="2"] .co-win-course-eval .win .score-star .btn-publish {
  line-height: 52px;
  font-size: 28px;
}

[data-dpr="3"] .co-win-course-eval .win .score-star .btn-publish {
  line-height: 78px;
  font-size: 42px;
}

.co-win-course-eval .win .score-star .btn-publish.disabled {
  color: #999;
  background-color: #eee;
  font-weight: normal;
}

.co-win-course-eval .win .desc {
  color: #FF9400;
  padding: 0.213333rem 0.533333rem 0;
}

[data-dpr="1"] .co-win-course-eval .win .desc {
  font-size: 16px;
}

[data-dpr="2"] .co-win-course-eval .win .desc {
  font-size: 32px;
}

[data-dpr="3"] .co-win-course-eval .win .desc {
  font-size: 48px;
}

.co-win-course-eval .win .desc .empty-str {
  color: #999;
  /*px*/
}

.co-win-course-eval .win .content-wrap {
  padding: 0.533333rem;
}

.co-win-course-eval .win .textarea-wrap {
  background-color: #f7f7f7;
  padding: 0 0.533333rem 0.533333rem;
}

.co-win-course-eval .win .textarea-wrap textarea {
  display: block;
  width: 100%;
  height: 4.2em;
  -webkit-box-sizing: content-box;
     -moz-box-sizing: content-box;
          box-sizing: content-box;
  color: #333;
  line-height: 1.4;
  border: none;
  padding-top: 0.533333rem;
  background-color: transparent;
  outline: none;
  resize: none;
}

[data-dpr="1"] .co-win-course-eval .win .textarea-wrap textarea {
  font-size: 15px;
}

[data-dpr="2"] .co-win-course-eval .win .textarea-wrap textarea {
  font-size: 30px;
}

[data-dpr="3"] .co-win-course-eval .win .textarea-wrap textarea {
  font-size: 45px;
}

.co-win-course-eval .win .textarea-wrap .str-len {
  margin-top: 0.533333rem;
  color: #999;
  text-align: right;
}

[data-dpr="1"] .co-win-course-eval .win .textarea-wrap .str-len {
  font-size: 12px;
}

[data-dpr="2"] .co-win-course-eval .win .textarea-wrap .str-len {
  font-size: 24px;
}

[data-dpr="3"] .co-win-course-eval .win .textarea-wrap .str-len {
  font-size: 36px;
}

.co-win-course-eval .win .btn-close {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  padding: 0.533333rem;
  color: #999;
}

[data-dpr="1"] .co-win-course-eval .win .btn-close {
  font-size: 20px;
}

[data-dpr="2"] .co-win-course-eval .win .btn-close {
  font-size: 40px;
}

[data-dpr="3"] .co-win-course-eval .win .btn-close {
  font-size: 60px;
}

.co-win-course-eval .win .eval-later {
  padding: 0.266667rem 0;
  margin: -0.266667rem 0 0.266667rem;
  text-align: center;
  color: #999;
}

[data-dpr="1"] .co-win-course-eval .win .eval-later {
  font-size: 12px;
}

[data-dpr="2"] .co-win-course-eval .win .eval-later {
  font-size: 24px;
}

[data-dpr="3"] .co-win-course-eval .win .eval-later {
  font-size: 36px;
}

.co-win-course-eval.co-win-course-eval-enter {
  -webkit-transition: opacity .3s;
  -moz-transition: opacity .3s;
  transition: opacity .3s;
  opacity: 0;
}

.co-win-course-eval.co-win-course-eval-enter .win {
  -webkit-transform: translate3d(0, 100%, 0);
     -moz-transform: translate3d(0, 100%, 0);
          transform: translate3d(0, 100%, 0);
  -webkit-transition: -webkit-transform .5s;
  transition: -webkit-transform .5s;
  -moz-transition: transform .5s, -moz-transform .5s;
  transition: transform .5s;
  transition: transform .5s, -webkit-transform .5s, -moz-transform .5s;
}

.co-win-course-eval.co-win-course-eval-enter-active {
  opacity: 1;
}

.co-win-course-eval.co-win-course-eval-enter-active .win {
  -webkit-transform: translate3d(0, 0, 0);
     -moz-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}

.co-win-course-eval.co-win-course-eval-exit .win {
  -webkit-transform: translate3d(0, 0, 0);
     -moz-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  -webkit-transition: -webkit-transform .3s;
  transition: -webkit-transform .3s;
  -moz-transition: transform .3s, -moz-transform .3s;
  transition: transform .3s;
  transition: transform .3s, -webkit-transform .3s, -moz-transform .3s;
}

.co-win-course-eval.co-win-course-eval-exit-active .win {
  -webkit-transform: translate3d(0, 100%, 0);
     -moz-transform: translate3d(0, 100%, 0);
          transform: translate3d(0, 100%, 0);
}