.show-user-box {
  position: absolute;
  left: 0;
  z-index: 6;
  bottom: 0;
  width: 100%;
  background: #FFF0F0;
}

.show-user-box > div {
  width: 100%;
  height: 100%;
  position: relative;
  display: -webkit-box;
  display: -webkit-flex;
