.reprint-push-box {
  position: absolute;
  bottom: 0;
  top: 0;
  width: 100%;
  z-index: 999;
  overflow: hidden;
}

.reprint-push-box .bg {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.reprint-push-box .bg.haibao-bg {
  position: absolute;
  top: 0;
  bottom: 0;
}

.reprint-push-box .main {
  position: absolute;
  bottom: 0;
  width: 100%;
  background: #fff;
  -moz-animation: mymove 0.5s ease-out;
       animation: mymove 0.5s ease-out;
  -webkit-animation: mymove 0.5s ease-out;
}

