.co-carousel-container {
  position: relative;
}

.co-carousel-container .carousel-indicator {
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
     -moz-transform: translateX(-50%);
          transform: translateX(-50%);
}

[data-dpr="1"] .co-carousel-container .carousel-indicator {
  bottom: 10px;
}

[data-dpr="2"] .co-carousel-container .carousel-indicator {
  bottom: 20px;
}

[data-dpr="3"] .co-carousel-container .carousel-indicator {
  bottom: 30px;
}

.co-carousel-container .carousel-indicator-item {
  display: inline-block;
  width: 0.2rem;
  height: 0.08rem;
  -webkit-border-radius: 0.04rem;
          border-radius: 0.04rem;
  background-color: #ffffff;
  opacity: .3;
  cursor: pointer;
  -webkit-transition: all .5s;
  -moz-transition: all .5s;
  transition: all .5s;
  margin: 0 0.04rem;
}

.co-carousel-container .carousel-indicator-item.active {
  width: 0.333333rem;
  opacity: 1;
}