.riding-latern {
  padding: 0.266667rem;
  cursor: pointer;
  background: #FFF9E6;
  color: #FF9F00;
}

[data-dpr="1"] .riding-latern {
  font-size: 12px;
}

[data-dpr="2"] .riding-latern {
  font-size: 24px;
}

[data-dpr="3"] .riding-latern {
  font-size: 36px;
}

.riding-latern .riding-latern-wrap {
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
     -moz-box-align: center;
          align-items: center;
}

.riding-latern .riding-latern-wrap .img-wrap {
  margin-right: 0.133333rem;
  display: inline-block;
  width: 0.32rem;
  height: 0.32rem;
  vertical-align: middle;
  background: url(data:image/png;base64,) center center no-repeat;
  -webkit-background-size: 100% auto;
          background-size: 100% auto;
}

.riding-latern .riding-latern-wrap .content-wrap {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
     -moz-box-flex: 1;
          flex: 1;
  width: 0;
  overflow: hidden;
}

.riding-latern .riding-latern-wrap .content-wrap .content {
  display: inline-block;
  white-space: nowrap;
  line-height: normal;
}