  cursor: pointer;
  right: 0.4rem;
  bottom: 3.466667rem;
  -webkit-transform-origin: bottom center;
     -moz-transform-origin: bottom center;
          transform-origin: bottom center;
  -webkit-animation: swing 2s 0.15s linear infinite;
     -moz-animation: swing 2s 0.15s linear infinite;
