.flex-body {
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
     -moz-box-orient: vertical;
     -moz-box-direction: normal;
          flex-direction: column;
  height: 100%;
  width: 100%;
  overflow: hidden;
}

.flex-main-h,
.flex-main-s {
  position: relative;
  height: 100%;
  width: 100%;
  -webkit-box-flex: 1;
  -webkit-flex: 1;
     -moz-box-flex: 1;
          flex: 1;
  overflow: hidden;
}

.flex-main-s {
  -webkit-overflow-scrolling: touch;
  z-index: 1;
  overflow: auto;
  overflow-x: hidden;
}

.flex-other {
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
}

.clearfix {
  *zoom: 1;
}

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0;
}

.clearline {
  *zoom: 1;
  height: 0;
  line-height: 0;
  font-size: 0;
  overflow: hidden;
  clear: both;
}

.clearperfect {
  clear: both;
  height: 0;
  overflow: hidden;
}

.input-image {
  position: relative;
  overflow: hidden;
}

.input-image div {
  position: absolute !important;
  right: 0;
  top: 0;
}

.input-image div input {
  position: absolute;
  right: 0;
  bottom: 0;
  opacity: 0;
  margin: 0;
  padding: 0;
  font-size: 6.4rem;
  cursor: pointer;
  z-index: 10;
  opacity: 0;
}

.flex-full-scroll-container #app > div {
  height: 100%;
}

.flex-full-scroll-container #app > div > div {
  height: 100%;
}

[class*=btn] {
  cursor: pointer;
}