.co-switch.sm {
  position: relative;
  display: inline-block;
  width: 0.866667rem;
  height: 0.533333rem;
  -webkit-border-radius: 0.533333rem;
          border-radius: 0.533333rem;
  cursor: pointer;
  background: #d6d6d6;
  -webkit-transition: .5s;
  -moz-transition: .5s;
  transition: .5s;
  border: 2px solid #cccccc;
}

.co-switch.sm:after {
  position: absolute;
  display: block;
  content: "";
  left: -0.026667rem;
  right: auto;
  top: -0.013333rem;
  width: 0.533333rem;
  height: 0.533333rem;
  -webkit-border-radius: 0.533333rem;
          border-radius: 0.533333rem;
  background: #ffffff;
  border: 1px solid #cccccc;
  -webkit-transition: .5s;
  -moz-transition: .5s;
  transition: .5s;
}

