.common-empty {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
     -moz-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 4rem;
  text-align: center;
  color: #999;
  line-height: 1.4;
  z-index: 3;
}

[data-dpr="1"] .common-empty {
  font-size: 12px;
}

[data-dpr="2"] .common-empty {
  font-size: 24px;
}

[data-dpr="3"] .common-empty {
  font-size: 36px;
}

.common-empty .co-empty-box {
  padding-bottom: 0.426667rem;
}

.common-empty .co-empty-img {
  display: block;
  max-width: 3.786667rem;
  margin: 0 auto;
}

.common-empty-m {
  padding: 0.533333rem;
}

.common-empty-m .co-empty-img {
  display: block;
  max-width: 1.973333rem;
  margin: 0 auto;
}

.common-empty-m .co-empty-desc {
  margin-top: 0.213333rem;
  color: #b2b2b2;
  text-align: center;
  line-height: 1.4;
}

[data-dpr="1"] .common-empty-m .co-empty-desc {
  font-size: 12px;
}

[data-dpr="2"] .common-empty-m .co-empty-desc {
  font-size: 24px;
}

[data-dpr="3"] .common-empty-m .co-empty-desc {
  font-size: 36px;
}