.create-live-enter {
  padding: 0.4rem;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
}

.create-live-enter .head {
  width: 1.066667rem;
  height: 1.066667rem;
  overflow: hidden;
  -webkit-border-radius: 0.533333rem;
          border-radius: 0.533333rem;
}

.create-live-enter .head img {
  display: block;
  width: 100%;
  height: auto;
}

.create-live-enter .content {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
     -moz-box-flex: 1;
          flex: 1;
  padding-left: 0.266667rem;
  line-height: 0.533333rem;
}

.create-live-enter .content .name {
  color: #666;
}

[data-dpr="1"] .create-live-enter .content .name {
  font-size: 14px;
}

[data-dpr="2"] .create-live-enter .content .name {
  font-size: 28px;
}

[data-dpr="3"] .create-live-enter .content .name {
  font-size: 42px;
}

.create-live-enter .content .tips {
  color: #333;
}

[data-dpr="1"] .create-live-enter .content .tips {
  font-size: 15px;
}

[data-dpr="2"] .create-live-enter .content .tips {
  font-size: 30px;
}

[data-dpr="3"] .create-live-enter .content .tips {
  font-size: 45px;
}

.create-live-enter .content .tips > span {
  color: #F73657;
  float: right;
}

[data-dpr="1"] .create-live-enter .content .tips > span {
  font-size: 13px;
}

[data-dpr="2"] .create-live-enter .content .tips > span {
  font-size: 26px;
}

[data-dpr="3"] .create-live-enter .content .tips > span {
  font-size: 39px;
}