.playing-animate {
  display: inline-block;
  height: 0.293333rem;
  line-height: 0.533333rem;
  text-align: center;
  position: relative;
  vertical-align: bottom;
  will-change: animation;
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  overflow: hidden;
  width: 0.48rem;
}

.playing-animate span {
  will-change: animation, transform;
  display: inline-block;
  width: 0.04rem;
  margin-bottom: 0;
  background-color: #fff;
  margin-right: -0.16rem;
  -webkit-transform: translateY(70%);
     -moz-transform: translateY(70%);
          transform: translateY(70%);
  height: 0.32rem;
}

.playing-animate .line1 {
  -webkit-animation: playingLine 0.6s infinite ease-in-out alternate;
     -moz-animation: playingLine 0.6s infinite ease-in-out alternate;
          animation: playingLine 0.6s infinite ease-in-out alternate;
}

.playing-animate .line2 {
  -webkit-animation: playingLine 0.6s 0.2s infinite ease-in-out alternate;
     -moz-animation: playingLine 0.6s 0.2s infinite ease-in-out alternate;
          animation: playingLine 0.6s 0.2s infinite ease-in-out alternate;
}

.playing-animate .line3 {
  -webkit-animation: playingLine 0.6s 0.4s infinite ease-in-out alternate;
     -moz-animation: playingLine 0.6s 0.4s infinite ease-in-out alternate;
          animation: playingLine 0.6s 0.4s infinite ease-in-out alternate;
}

.playing-animate .line4 {
  -webkit-animation: playingLine 0.6s 0.6s infinite ease-in-out alternate;
     -moz-animation: playingLine 0.6s 0.6s infinite ease-in-out alternate;
          animation: playingLine 0.6s 0.6s infinite ease-in-out alternate;
}

@-webkit-keyframes playingLine {
  from {
    -webkit-transform: translateY(70%);
            transform: translateY(70%);
  }

  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@-moz-keyframes playingLine {
  from {
    -moz-transform: translateY(70%);
         transform: translateY(70%);
  }

  to {
    -moz-transform: translateY(0);
         transform: translateY(0);
  }
}

@keyframes playingLine {
  from {
    -webkit-transform: translateY(70%);
       -moz-transform: translateY(70%);
            transform: translateY(70%);
  }

  to {
    -webkit-transform: translateY(0);
       -moz-transform: translateY(0);
            transform: translateY(0);
  }
}