.bottom-branch-bar {
  padding: 0.4rem;
  background: transparent;
}

.bottom-branch-bar .button-list {
  text-align: center;
  line-height: 0.8rem;
}

.bottom-branch-bar .button-list > div {
  display: inline;
  color: #F73657;
  margin-right: 0.266667rem;
}

[data-dpr="1"] .bottom-branch-bar .button-list > div {
  font-size: 12px;
}

[data-dpr="2"] .bottom-branch-bar .button-list > div {
  font-size: 24px;
}

[data-dpr="3"] .bottom-branch-bar .button-list > div {
  font-size: 36px;
}

.bottom-branch-bar .button-list > div::after {
  content: '';
  display: inline-block;
  height: 0.186667rem;
  background: #999;
  margin-left: 0.266667rem;
}

[data-dpr="1"] .bottom-branch-bar .button-list > div::after {
  width: 1px;
}

[data-dpr="2"] .bottom-branch-bar .button-list > div::after {
  width: 2px;
}

[data-dpr="3"] .bottom-branch-bar .button-list > div::after {
  width: 3px;
}

.bottom-branch-bar .button-list > div:last-child::after {
  display: none;
}

.bottom-branch-bar .tec-logo {
  text-align: center;
  color: #999;
  line-height: 0.533333rem;
}

[data-dpr="1"] .bottom-branch-bar .tec-logo {
  font-size: 10px;
}

[data-dpr="2"] .bottom-branch-bar .tec-logo {
  font-size: 20px;
}

[data-dpr="3"] .bottom-branch-bar .tec-logo {
  font-size: 30px;
}

.bottom-branch-bar .tec-logo::before {
  content: '';
  display: inline-block;
  width: 0.933333rem;
  height: 0.4rem;
  vertical-align: middle;
  background: url(data:image/png;base64,) center center no-repeat;
  -webkit-background-size: 100% auto;
          background-size: 100% auto;
  margin-right: 0.133333rem;
}