.co-share-success-modal {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 1;
}

.co-share-success-modal .bg {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.co-share-success-modal > main {
  width: 8rem;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
     -moz-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
     -moz-box-pack: center;
          justify-content: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
     -moz-box-orient: vertical;
     -moz-box-direction: normal;
          flex-direction: column;
  -webkit-box-align: center;
  -webkit-align-items: center;
     -moz-box-align: center;
          align-items: center;
  background-color: #fff;
  -webkit-border-radius: 0.16rem;
          border-radius: 0.16rem;
  padding: 0.653333rem 0 1.2rem;
  overflow: hidden;
}

.co-share-success-modal > main .icon {
  background-image: url(data:image/png;base64,);
  width: 0.853333rem;
  height: 0.853333rem;
  -webkit-background-size: cover;
          background-size: cover;
  background-repeat: no-repeat;
}

.co-share-success-modal > main .title {
  color: #333;
  margin-top: 0.346667rem;
}

[data-dpr="1"] .co-share-success-modal > main .title {
  font-size: 15px;
}

[data-dpr="2"] .co-share-success-modal > main .title {
  font-size: 30px;
}

[data-dpr="3"] .co-share-success-modal > main .title {
  font-size: 45px;
}

.co-share-success-modal > main .img-container {
  margin-top: 0.533333rem;
  margin-bottom: 0.666667rem;
  width: 4rem;
  height: 4rem;
}

.co-share-success-modal > main .img-container img {
  width: 4rem;
  height: 4rem;
  margin: -6.666667rem;
  padding: 6.666667rem;
}

.co-share-success-modal > main .tips {
  color: #333;
  line-height: 1.6;
  text-align: center;
}

[data-dpr="1"] .co-share-success-modal > main .tips {
  font-size: 13px;
}

[data-dpr="2"] .co-share-success-modal > main .tips {
  font-size: 26px;
}

[data-dpr="3"] .co-share-success-modal > main .tips {
  font-size: 39px;
}

.co-share-success-modal > main .close {
  background-image: url(data:image/png;base64,);
  width: 0.853333rem;
  height: 0.853333rem;
  -webkit-background-size: 0.293333rem 0.293333rem;
          background-size: 0.293333rem;
  background-position: center;
  background-repeat: no-repeat;
  position: absolute;
  top: 0.093333rem;
  right: 0;
  cursor: pointer;
}