.guest-profit-container {
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
     -moz-box-orient: vertical;
     -moz-box-direction: normal;
          flex-direction: column;
  height: 100%;
}

.guest-profit-container .guest-tab-bar {
  background: #fff;
  padding: 0.266667rem 0.4rem;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
}

[data-dpr="1"] .guest-profit-container .guest-tab-bar {
  border-top: 1px solid #EEEEEE;
}

[data-dpr="2"] .guest-profit-container .guest-tab-bar {
  border-top: 2px solid #EEEEEE;
}

[data-dpr="3"] .guest-profit-container .guest-tab-bar {
  border-top: 3px solid #EEEEEE;
}

.guest-profit-container .guest-tab-bar .guest-tab {
  width: 1.866667rem;
  height: 0.72rem;
  -webkit-border-radius: 0.4rem;
          border-radius: 0.4rem;
  line-height: 0.72rem;
  background: #F7F7F7;
  color: #999999;
  text-align: center;
  margin: 0 0.133333rem;
}

[data-dpr="1"] .guest-profit-container .guest-tab-bar .guest-tab {
  font-size: 12px;
}

[data-dpr="2"] .guest-profit-container .guest-tab-bar .guest-tab {
  font-size: 24px;
}

[data-dpr="3"] .guest-profit-container .guest-tab-bar .guest-tab {
  font-size: 36px;
}

.guest-profit-container .guest-tab-bar .guest-tab.active {
  background: #fff0f0;
  color: #F73657;
}

.guest-profit-container .scroller-wrap {
  position: relative;
  -webkit-box-flex: 1;
  -webkit-flex: 1;
     -moz-box-flex: 1;
          flex: 1;
}