.co-scroll-view {
  width: 100%;
  height: 100%;
  overflow-y: auto;
  -webkit-text-size-adjust: none;
          text-size-adjust: none;
}

.co-load-status {
  line-height: 1.6rem;
  color: #B2B2B2;
  text-align: center;
  white-space: nowrap;
}

[data-dpr="1"] .co-load-status {
  font-size: 11px;
}

[data-dpr="2"] .co-load-status {
  font-size: 22px;
}

[data-dpr="3"] .co-load-status {
  font-size: 33px;
}

.co-load-status div {
  color: #666;
}

.co-loading-ellipsis {
  position: relative;
}

.co-loading-ellipsis::before {
  content: "...";
  color: transparent;
}

@-webkit-keyframes coLoadingEllipsis {
  33% {
    width: 100%;
  }

  34% {
    width: 33%;
  }

  66% {
    width: 33%;
  }

  67% {
    width: 66%;
  }

  99% {
    width: 66%;
  }

  100% {
    width: 100%;
  }
}

@-moz-keyframes coLoadingEllipsis {
  33% {
    width: 100%;
  }

  34% {
    width: 33%;
  }

  66% {
    width: 33%;
  }

  67% {
    width: 66%;
  }

  99% {
    width: 66%;
  }

  100% {
    width: 100%;
  }
}

@keyframes coLoadingEllipsis {
  33% {
    width: 100%;
  }

  34% {
    width: 33%;
  }

  66% {
    width: 33%;
  }

  67% {
    width: 66%;
  }

  99% {
    width: 66%;
  }

  100% {
    width: 100%;
  }
}

.co-loading-ellipsis::after {
  content: "...";
  position: absolute;
  left: 0;
  overflow: hidden;
  width: 100%;
  -webkit-animation: coLoadingEllipsis 1s infinite;
     -moz-animation: coLoadingEllipsis 1s infinite;
          animation: coLoadingEllipsis 1s infinite;
}

.co-scroll-view-empty {
  position: static;
  -webkit-transform: none;
     -moz-transform: none;
          transform: none;
  min-height: 100%;
  width: 100%;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
     -moz-box-orient: vertical;
     -moz-box-direction: normal;
          flex-direction: column;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
     -moz-box-pack: center;
          justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
     -moz-box-align: center;
          align-items: center;
  padding: 0.533333rem 0;
  -webkit-box-sizing: border-box;
     -moz-box-sizing: border-box;
          box-sizing: border-box;
}