.novice-guide-step {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: 99;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
}

.novice-guide-step .novice-guide-step-bottom {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 1.333333rem;
  background: yellow;
}

.novice-guide-step .novice-guide-step-container {
  width: 100%;
  overflow: hidden;
}

.novice-guide-step .novice-guide-step-bg {
  position: relative;
  min-height: 100%;
}

.novice-guide-step .novice-guide-step-bg > img {
  width: 100%;
  height: auto;
}

.novice-guide-step .step-point {
  position: absolute;
  -webkit-border-radius: 0.066667rem;
          border-radius: 0.066667rem;
  -webkit-transform: translate(-9999px, -9999px);
     -moz-transform: translate(-9999px, -9999px);
          transform: translate(-9999px, -9999px);
  border: 9999px solid rgba(0, 0, 0, 0.6);
}

.novice-guide-step .step-point.border::after {
  content: "";
  position: absolute;
  left: -0.133333rem;
  right: -0.133333rem;
  top: -0.133333rem;
  bottom: -0.133333rem;
  -webkit-border-radius: 0.066667rem;
          border-radius: 0.066667rem;
}

[data-dpr="1"] .novice-guide-step .step-point.border::after {
  border: 0.5px dashed #FFF;
}

[data-dpr="2"] .novice-guide-step .step-point.border::after {
  border: 1px dashed #FFF;
}

[data-dpr="3"] .novice-guide-step .step-point.border::after {
  border: 1.5px dashed #FFF;
}

.novice-guide-step .novice-guide-step-content {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
}

.novice-guide-step .step-guide-source.up {
  width: 0.48rem;
  height: 2.306667rem;
  background: url(data:image/png;base64,) no-repeat center/contain;
}

.novice-guide-step .step-guide-source.down {
  width: 0.48rem;
  height: 2.306667rem;
  background: url(data:image/png;base64,) no-repeat center/contain;
}

.novice-guide-step .step-guide-source.ok {
  width: 4.64rem;
  height: 1.2rem;
  background: url(/wechat-react/img/guide-ok.214bd4d6b92dc974f2fb884b1c56cc88.png) no-repeat center/contain;
}