/**
 * 1px
 */

.co-auto-fixed-nav {
  position: absolute;
  z-index: 3;
  top: 0;
  width: 100%;
  background-color: #fff;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  -webkit-transform: translateY(-100%);
     -moz-transform: translateY(-100%);
          transform: translateY(-100%);
  -webkit-transition: -webkit-transform .2s;
  transition: -webkit-transform .2s;
  -moz-transition: transform .2s, -moz-transform .2s;
  transition: transform .2s;
  transition: transform .2s, -webkit-transform .2s, -moz-transform .2s;
}

.co-auto-fixed-nav::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  -webkit-transform-origin: 0 100%;
     -moz-transform-origin: 0 100%;
          transform-origin: 0 100%;
}

[data-dpr="1"] .co-auto-fixed-nav::after {
  height: 0.5px;
}

[data-dpr="2"] .co-auto-fixed-nav::after {
  height: 1px;
}

[data-dpr="3"] .co-auto-fixed-nav::after {
  height: 1.5px;
}

@media (-webkit-min-device-pixel-ratio: 3.1) {
  .co-auto-fixed-nav::after {
    -webkit-transform: scaleY(0.3);
       -moz-transform: scaleY(0.3);
            transform: scaleY(0.3);
  }
}

.co-auto-fixed-nav.co-auto-fixed-nav-show {
  -webkit-transform: translateY(0);
     -moz-transform: translateY(0);
          transform: translateY(0);
}

.co-auto-fixed-nav.co-auto-fixed-nav-static {
  position: relative;
  -webkit-transform: none;
     -moz-transform: none;
          transform: none;
}

.co-auto-fixed-nav li {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
     -moz-box-flex: 1;
          flex: 1;
  position: relative;
  text-align: center;
  color: #333;
}

[data-dpr="1"] .co-auto-fixed-nav li {
  height: 55px;
  line-height: 55px;
  font-size: 16px;
}

[data-dpr="2"] .co-auto-fixed-nav li {
  height: 110px;
  line-height: 110px;
  font-size: 32px;
}

[data-dpr="3"] .co-auto-fixed-nav li {
  height: 165px;
  line-height: 165px;
  font-size: 48px;
}

.co-auto-fixed-nav li::after {
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
     -moz-transform: translateX(-50%);
          transform: translateX(-50%);
  background-color: #F73657;
  -webkit-border-radius: 0.053333rem;
          border-radius: 0.053333rem;
}

[data-dpr="1"] .co-auto-fixed-nav li::after {
  bottom: 11px;
  width: 18px;
  height: 2px;
}

[data-dpr="2"] .co-auto-fixed-nav li::after {
  bottom: 22px;
  width: 36px;
  height: 4px;
}

[data-dpr="3"] .co-auto-fixed-nav li::after {
  bottom: 33px;
  width: 54px;
  height: 6px;
}

.co-auto-fixed-nav li.active {
  color: #F73657;
  font-weight: bold;
}

.co-auto-fixed-nav li.active::after {
  content: "";
}