.co-bullet-screen {
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  overflow: hidden;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0.8)), to(rgba(255, 255, 255, 0.95)));
  background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.95));
  background: -moz-linear-gradient(top, rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.95));
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.95));
}

.co-bullet-screen.co-bullet-screen-enter {
  opacity: .01;
  -webkit-transition: opacity 1s;
  -moz-transition: opacity 1s;
  transition: opacity 1s;
}

.co-bullet-screen.co-bullet-screen-enter.co-bullet-screen-enter-active {
  opacity: 1;
}

.co-bullet-screen.co-bullet-screen-exit {
  opacity: 1;
  -webkit-transition: opacity .3s;
  -moz-transition: opacity .3s;
  transition: opacity .3s;
}

.co-bullet-screen.co-bullet-screen-exit.co-bullet-screen-exit-active {
  opacity: .01;
}

.co-bullet-screen .reference {
  width: 0;
  height: 0.8rem;
  opacity: 0;
}

.co-bullet-screen .co-bullet {
  position: absolute;
  font-size: 0;
  color: #fff;
  white-space: nowrap;
  left: 100%;
  -webkit-transform: translate3d(0, 0, 0);
     -moz-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  -webkit-perspective: 1000;
     -moz-perspective: 1000;
          perspective: 1000;
}

.co-bullet-screen .co-bullet .avatar {
  display: inline-block;
  vertical-align: middle;
  width: 0.8rem;
  height: 0.8rem;
  border: 1px solid #666;
  -webkit-border-radius: 50%;
          border-radius: 50%;
  background-repeat: no-repeat;
  background-position: center;
  -webkit-background-size: cover;
          background-size: cover;
}

.co-bullet-screen .co-bullet .content {
  display: -webkit-inline-box;
  display: -webkit-inline-flex;
  display: -moz-inline-box;
  display: inline-flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
     -moz-box-align: center;
          align-items: center;
  vertical-align: middle;
  padding: 0 1em;
  margin-left: 0.16rem;
  font-size: 0.32rem;
  line-height: normal;
  height: 0.693333rem;
  -webkit-border-radius: 0.346667rem;
          border-radius: 0.346667rem;
  background-color: rgba(0, 0, 0, 0.6);
}

.co-bullet-screen .co-bullet.role-user .avatar {
  border-color: #FF9F00;
}

.co-bullet-screen .co-bullet.role-user .content {
  background-color: #FF9F00;
}

.co-bullet-screen .co-bullet.current-user .avatar {
  border-color: #F73657;
}

.co-bullet-screen .co-bullet.current-user .content {
  background-color: #F73657;
}

