.buy-camp-ad {
  padding: 0.426667rem 0.426667rem 0;
  margin-bottom: 0.32rem;
  position: relative;
}

.buy-camp-ad.max-btm {
  margin: 0 0 0.64rem;
}

.buy-camp-ad img {
  width: 100%;
  height: auto;
}

.buy-camp-ad span {
  position: absolute;
  top: 0.053333rem;
  right: 0.213333rem;
  width: 0.48rem;
  height: 0.48rem;
  background: #666;
  -webkit-border-radius: 50%;
          border-radius: 50%;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
     -moz-box-pack: center;
          justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
     -moz-box-align: center;
          align-items: center;
  color: #fff;
  z-index: 10;
}

[data-dpr="1"] .buy-camp-ad span {
  font-size: 8px;
  border: 1px solid #fff;
}

[data-dpr="2"] .buy-camp-ad span {
  font-size: 16px;
  border: 2px solid #fff;
}

[data-dpr="3"] .buy-camp-ad span {
  font-size: 24px;
  border: 3px solid #fff;
}

.buy-camp-animate-enter {
  opacity: 0;
}

.buy-camp-animate-enter-active {
  opacity: 1;
  -webkit-transition: opacity 1s, -webkit-transform 1s;
  transition: opacity 1s, -webkit-transform 1s;
  -moz-transition: opacity 1s, transform 1s, -moz-transform 1s;
  transition: opacity 1s, transform 1s;
  transition: opacity 1s, transform 1s, -webkit-transform 1s, -moz-transform 1s;
}

.buy-camp-animate-exit {
  opacity: 1;
}

.buy-camp-animate-exit-active {
  opacity: 0;
  -webkit-transition: opacity 1s, -webkit-transform 1s;
  transition: opacity 1s, -webkit-transform 1s;
  -moz-transition: opacity 1s, transform 1s, -moz-transform 1s;
  transition: opacity 1s, transform 1s;
  transition: opacity 1s, transform 1s, -webkit-transform 1s, -moz-transform 1s;
}