.co-image-viewer {
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  margin: 0 auto;
  text-align: center;
  height: 100%;
  overflow: hidden;
}

[data-dpr="1"] .co-image-viewer {
  max-width: 640px;
  min-width: 320px;
}

[data-dpr="2"] .co-image-viewer {
  max-width: 1280px;
  min-width: 640px;
}

[data-dpr="3"] .co-image-viewer {
  max-width: 1920px;
  min-width: 960px;
}

.co-image-viewer .bg {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #000;
  opacity: .5;
  z-index: 1001;
}

.co-image-viewer .close-btn {
  position: absolute;
  z-index: 1003;
  right: 0.4rem;
  top: 0.4rem;
  width: 0.906667rem;
  height: 0.906667rem;
  line-height: 0.906667rem;
  text-align: center;
  background-position: 0 0;
  background-color: rgba(0, 0, 0, 0.5);
  border: solid 2px #fff;
  -webkit-border-radius: 50%;
          border-radius: 50%;
  color: #fff;
  cursor: pointer;
}

[data-dpr="1"] .co-image-viewer .close-btn {
  font-size: 20px;
}

[data-dpr="2"] .co-image-viewer .close-btn {
  font-size: 40px;
}

[data-dpr="3"] .co-image-viewer .close-btn {
  font-size: 60px;
}

.co-image-viewer img {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transition-property: margin,width,height;
  -moz-transition-property: margin,width,height;
  transition-property: margin,width,height;
  z-index: 1002;
}