.wrap {
  position: relative;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}

.wrap .redpointNormal {
  position: absolute;
  -webkit-border-radius: 50%;
          border-radius: 50%;
  background-color: red;
  color: #fff;
}

[data-dpr="1"] .wrap .redpointNormal {
  width: 8px;
  height: 8px;
}

[data-dpr="2"] .wrap .redpointNormal {
  width: 16px;
  height: 16px;
}

[data-dpr="3"] .wrap .redpointNormal {
  width: 24px;
  height: 24px;
}

.wrap .background {
  position: absolute;
  font-weight: normal;
  top: 0;
  left: 0;
}

[data-dpr="1"] .wrap .redpoint {
  right: -4px;
}

[data-dpr="2"] .wrap .redpoint {
  right: -8px;
}

[data-dpr="3"] .wrap .redpoint {
  right: -12px;
}

.wrap .red-btn-background {
  width: 100%;
  height: 100%;
}