.doctor-dialog .doctor-wrap {
  display: block;
  text-align: center;
  padding: 1.066667rem 0 0.8rem;
}

.doctor-dialog .logo-doctor {
  margin: 0 auto 0.666667rem auto;
  background: url(/wechat-react/img/colorful-doctor.6350fd89c16818c536ea80df17e71da7.png) no-repeat;
  -webkit-background-size: contain;
          background-size: contain;
}

[data-dpr="1"] .doctor-dialog .logo-doctor {
  width: 140.5px;
  height: 163.5px;
}

[data-dpr="2"] .doctor-dialog .logo-doctor {
  width: 281px;
  height: 327px;
}

[data-dpr="3"] .doctor-dialog .logo-doctor {
  width: 421.5px;
  height: 490.5px;
}

.doctor-dialog .title {
  display: block;
  text-align: center;
  color: #575757;
  line-height: 0.666667rem;
  font-weight: bold;
  margin-top: 0.533333rem;
}

[data-dpr="1"] .doctor-dialog .title {
  font-size: 20px;
}

[data-dpr="2"] .doctor-dialog .title {
  font-size: 40px;
}

[data-dpr="3"] .doctor-dialog .title {
  font-size: 60px;
}

.doctor-dialog .descript {
  color: #9b9ea3;
  display: block;
  text-align: center;
  line-height: 0.4rem;
  margin-top: 0.266667rem;
}

[data-dpr="1"] .doctor-dialog .descript {
  font-size: 14px;
}

[data-dpr="2"] .doctor-dialog .descript {
  font-size: 28px;
}

[data-dpr="3"] .doctor-dialog .descript {
  font-size: 42px;
}