/*  */

body,
h1,
h2,
h3,
h4,
h5,
h6,
hr,
p,
blockquote,
dl,
dt,
dd,
ul,
ol,
li,
pre,
fieldset,
lengend,
button,
input,
textarea,
th,
td {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
       box-sizing: border-box;
  /* table elements  */
  margin: 0;
  padding: 0;
  word-wrap: break-word;
  word-break: break-all;
  -webkit-appearance: none;
}

/*  */

body,
button,
input,
select,
textarea {
  /* for ie */
  /*font: 12px/1 Tahoma, Helvetica, Arial, "", sans-serif;*/
  font: 12px/1 "\5FAE\8F6F\96C5\9ED1",Tahoma, Helvetica, Arial, '\5B8B\4F53', sans-serif;
}

h1 {
  font-size: 18px;
}

h2 {
  font-size: 16px;
}

h3 {
  font-size: 14px;
}

h4,
h5,
h6 {
  font-size: 100%;
  /*no*/
}

address,
cite,
dfn,
em,
var {
  font-style: normal;
}

/*  */

code,
kbd,
pre,
samp,
tt {
  font-family: "\5FAE\8F6F\96C5\9ED1",Tahoma, Helvetica, Arial, '\5B8B\4F53', sans-serif;
}

/*  */

small {
  font-size: 12px;
}

/*  12px  small  */

/*  */

ul,
ol {
  list-style: none;
}

/*  */

a {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

a:active {
  text-decoration: none;
}

abbr[title],
acronym[title] {
  cursor: help;
  /* 1.ie6  abbr; 2.ie6  */
  border-bottom: 1px dotted;
}

q:before,
q:after {
  content: '';
}

/*  */

legend {
  color: #000;
}

/* for ie6 */

fieldset,
img {
  border: none;
}

/* img  img  */

/* optgroup  */

/*  */

table {
  border-spacing: 0;
  border-collapse: collapse;
}

/*  hr */

hr {
  height: 1px;
  border: none;
}

/* ie */

html {
  overflow-y: scroll;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

/*input[type=number]*/

input[type='number']::-webkit-outer-spin-button,
input[type='number']::-webkit-inner-spin-button {
  margin: 0;
  -webkit-appearance: none;
}

/* placeholder */

::-webkit-input-placeholder {
  color: #b2b2b2;
}

:-ms-input-placeholder {
  color: #b2b2b2;
}

:-moz-placeholder {
  color: #b2b2b2;
}

::-moz-placeholder {
  color: #b2b2b2;
}

body {
  color: #333;
  background-color: #f9f9f9;
}