.confirm-dialog-vintro-wrap {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
  z-index: 1000;
}

.confirm-dialog-vintro-wrap .confirm-dialog-vintro {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: #ffffff;
  overflow: hidden;
  margin: auto;
}

