.focus-bar-box {
  padding: 0.266667rem 0;
  line-height: 0.48rem;
  background-color: #fff;
}

.focus-bar-box .tips {
  color: #333;
}

[data-dpr="1"] .focus-bar-box .tips {
  font-size: 13px;
}

[data-dpr="2"] .focus-bar-box .tips {
  font-size: 26px;
}

[data-dpr="3"] .focus-bar-box .tips {
  font-size: 39px;
}

.focus-bar-box .focus-control {
  float: right;
  padding-right: 0.133333rem;
}

.focus-bar-box .focus-control .btn-focus {
  padding: 0.053333rem 0.266667rem;
  color: #fff;
  background-color: #f83657;
  -webkit-border-radius: 0.666667rem;
          border-radius: 0.666667rem;
  vertical-align: middle;
}

[data-dpr="1"] .focus-bar-box .focus-control .btn-focus {
  font-size: 12px;
}

[data-dpr="2"] .focus-bar-box .focus-control .btn-focus {
  font-size: 24px;
}

[data-dpr="3"] .focus-bar-box .focus-control .btn-focus {
  font-size: 36px;
}

.focus-bar-box .focus-control .icon_cancel {
  color: #b4b4b4;
  vertical-align: middle;
  margin-left: 0.133333rem;
}

[data-dpr="1"] .focus-bar-box .focus-control .icon_cancel {
  font-size: 24px;
}

[data-dpr="2"] .focus-bar-box .focus-control .icon_cancel {
  font-size: 48px;
}

[data-dpr="3"] .focus-bar-box .focus-control .icon_cancel {
  font-size: 72px;
}

.focus-bar-box:before {
  content: '';
  display: inline-block;
  background: url(/wechat-react/img/ql-logo.7820790429477d83277de7893c1dfa47.png) center center no-repeat;
  -webkit-background-size: 100% auto;
          background-size: 100% auto;
  vertical-align: middle;
  margin: -0.026667rem 0.133333rem 0;
}

[data-dpr="1"] .focus-bar-box:before {
  width: 28px;
  height: 28px;
}

[data-dpr="2"] .focus-bar-box:before {
  width: 56px;
  height: 56px;
}

[data-dpr="3"] .focus-bar-box:before {
  width: 84px;
  height: 84px;
}

.focus-bar-box .qlqrcode-dialog {
  z-index: 300;
}

.focus-bar-box .qlqrcode-dialog .content {
  text-align: center;
  padding: 0.533333rem 0.266667rem;
}

.focus-bar-box .qlqrcode-dialog .content .qrcode {
  width: 60%;
  margin: 0 auto;
}

.focus-bar-box .qlqrcode-dialog .content .qrcode img {
  width: 100%;
  height: auto;
}