.try-list-share-mask {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.6);
  z-index: 100;
}

.try-list-share-mask .arrow {
  position: absolute;
  top: 0;
  right: 0.373333rem;
  -webkit-transform: translate(0, -130%);
     -moz-transform: translate(0, -130%);
          transform: translate(0, -130%);
  text-align: right;
}

.try-list-share-mask .arrow .pic-arrow {
  height: 3.2rem;
}

.try-list-share-mask .middle-section {
  position: absolute;
  line-height: normal;
  left: 0;
  right: 0;
  top: 46%;
  -webkit-transform: translate(0, -50%);
     -moz-transform: translate(0, -50%);
          transform: translate(0, -50%);
}

.try-list-share-mask .middle-section .middle-section-inner {
  width: 8.373333rem;
  background: white;
  -webkit-border-radius: 0.426667rem;
          border-radius: 0.426667rem;
  text-align: center;
  padding: 1.08rem 0 0.493333rem;
  margin: 0 auto;
}

.try-list-share-mask .middle-section .icon-share {
  position: absolute;
  top: 0;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
     -moz-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 1.52rem;
  height: 1.52rem;
}

.try-list-share-mask .middle-section .title {
  height: 0.6rem;
  font-size: 0.426667rem;
  font-family: PingFangSC-Semibold;
  font-weight: 600;
  color: #333333;
  text-align: center;
  margin-bottom: 0.026667rem;
}

.try-list-share-mask .middle-section .prompt {
  font-size: 0.373333rem;
  font-family: PingFangSC-Medium;
  font-weight: 500;
  color: #666666;
  margin-bottom: 0.346667rem;
}

.try-list-share-mask .middle-section .prompt span {
  color: #f73657;
}

.try-list-share-mask .middle-section .invite {
  font-size: 0.32rem;
  font-family: PingFangSC-Regular;
  font-weight: 400;
  color: #666666;
  text-align: center;
}

.try-list-share-mask .middle-section .require {
  font-size: 0.32rem;
  font-family: PingFangSC-Regular;
  font-weight: 400;
  color: #666666;
  text-align: center;
  margin-bottom: 0.266667rem;
}

.try-list-share-mask .middle-section .head-img-list .head-img {
  display: inline-block;
  width: 0.826667rem;
  height: 0.84rem;
  -webkit-border-radius: 50%;
          border-radius: 50%;
  vertical-align: middle;
}

.try-list-share-mask .middle-section .head-img-list .head-img + .head-img {
  margin-left: 0.346667rem;
}

.try-list-share-mask .bottom {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  padding-top: 0.266667rem;
}

.try-list-share-mask .bottom .prompt {
  text-align: center;
  font-size: 0.32rem;
  font-family: PingFangSC-Regular;
  font-weight: 400;
  color: white;
  line-height: normal;
  margin-bottom: 0.133333rem;
}

.try-list-share-mask .bottom .qr-code-wrap {
  text-align: center;
}

.try-list-share-mask .bottom .qr-code-wrap .qr-code {
  width: 2.4rem;
  height: 2.4rem;
}