@charset "UTF-8";

.intro-page-coupon-btn {
  height: 0.64rem;
  font-size: 0.32rem;
  color: #FFF;
  background-image: -webkit-gradient(linear, right top, left top, from(#FF7D00), to(#FFA200));
  background-image: -webkit-linear-gradient(right, #FF7D00 0%, #FFA200 100%);
  background-image: -moz-linear-gradient(right, #FF7D00 0%, #FFA200 100%);
  background-image: linear-gradient(-90deg, #FF7D00 0%, #FFA200 100%);
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  -webkit-border-radius: 0.08rem;
          border-radius: 0.08rem;
  line-height: 0.64rem;
  overflow: hidden;
  -webkit-background-clip: content-box;
          background-clip: content-box;
  padding: 0.026667rem;
}

.intro-page-coupon-btn.fixed {
  position: fixed;
  right: 0.533333rem;
  z-index: 10;
}

.intro-page-coupon-btn .tips,
.intro-page-coupon-btn .price {
  padding: 0 0.106667rem;
  height: 100%;
}

.intro-page-coupon-btn .tips {
  position: relative;
  width: 2.066667rem;
  text-align: center;
}

.intro-page-coupon-btn .tips.animation {
  -webkit-transition: width .3s linear;
  -moz-transition: width .3s linear;
  transition: width .3s linear;
}

.intro-page-coupon-btn .tips.receive {
  width: 3rem;
}

.intro-page-coupon-btn .tips.auto-bind {
  width: 2.733333rem;
}

.intro-page-coupon-btn .tips::before,
.intro-page-coupon-btn .tips::after {
  content: '';
  position: absolute;
  right: 0;
  width: 0.106667rem;
  height: 0.106667rem;
  background: #FFF;
  -webkit-border-radius: 50%;
          border-radius: 50%;
}

.intro-page-coupon-btn .tips::before {
  top: 0;
  -webkit-transform: translate(50%, -50%);
     -moz-transform: translate(50%, -50%);
          transform: translate(50%, -50%);
}

.intro-page-coupon-btn .tips::after {
  bottom: 0;
  -webkit-transform: translate(50%, 50%);
     -moz-transform: translate(50%, 50%);
          transform: translate(50%, 50%);
}

.intro-page-coupon-btn .price {
  padding: 0 0.106667rem;
}

.intro-page-coupon-btn .price::before {
  content: '\FFE5';
  font-size: 0.213333rem;
}

.intro-page-coupon-btn .more {
  padding: 0 0.106667rem;
  border-left: 1px dashed #FFF;
  font-size: 0.32rem;
}