          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  height: 1.333333rem;
  background-color: #fff;
}

.__tab-view .tab-list::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  -webkit-transform-origin: 0 100%;
     -moz-transform-origin: 0 100%;
          transform-origin: 0 100%;
}

