.curtain-container {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 99;
}

.curtain-container-close-btn {
  position: absolute;
  width: 0.666667rem;
  height: 0.666667rem;
}

.curtain-content-box {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 100;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
}

.curtain-content-box.with-close-btn {
  top: -moz-calc(50% + 0.4rem);
  top: calc(50% + 0.4rem);
}