.coral-join-dialog {
  text-align: center;
  z-index: 200;
}

.coral-join-dialog .co-dialog-content {
  background-color: rgba(0, 0, 0, 0);
  padding-bottom: 14%;
}

.coral-join-dialog .btn-close-ui {
  top: 94%;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
     -moz-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  width: 0.8rem;
  height: 0.8rem;
  color: #fff;
  font-size: 0.8rem;
}

.coral-join-dialog .coral-join-box {
  width: 100%;
  height: auto;
}

.coral-join-dialog .coral-joined-box {
  padding-bottom: 136%;
  background: url(https://img.qlchat.com/qlLive/coral/joined-box-bg.png) top center no-repeat;
  -webkit-background-size: 100% 100%;
          background-size: 100% 100%;
}

.coral-join-dialog .btn-join {
  display: block;
  height: 0.933333rem;
  width: 5.44rem;
  line-height: 0.933333rem;
  position: absolute;
  bottom: 20%;
  left: 50%;
  margin-left: -2.733333rem;
  color: #9f7741;
  font-size: 0.373333rem;
  letter-spacing: 0.053333rem;
  background: -webkit-gradient(linear, left top, left bottom, from(#ffdc7d), to(#ffca59));
  background: -webkit-linear-gradient(#ffdc7d, #ffca59);
  background: -moz-linear-gradient(#ffdc7d, #ffca59);
  background: linear-gradient(#ffdc7d, #ffca59);
  border: 1px solid #fee8c3;
  -webkit-border-radius: 0.666667rem;
          border-radius: 0.666667rem;
  text-align: center;
  -webkit-box-shadow: 0 0.133333rem 0.266667rem rgba(248, 150, 3, 0.5);
          box-shadow: 0 0.133333rem 0.266667rem rgba(248, 150, 3, 0.5);
}

.coral-join-dialog .head-img {
  position: absolute;
  top: 44%;
  left: 50%;
  -webkit-transform: translate(-50.1%, 0);
     -moz-transform: translate(-50.1%, 0);
          transform: translate(-50.1%, 0);
  width: 14.7%;
  -webkit-border-radius: 50%;
          border-radius: 50%;
}