.live-v-dialog {
  text-align: center;
}

.live-v-dialog .content:before {
  content: '';
  display: block;
  background: url(/wechat-react/img/jiav-box.1533f15de05272a4fa0407c0b765137f.png) center center no-repeat;
  -webkit-background-size: 40% auto;
          background-size: 40% auto;
  margin: 0.8rem auto 0.4rem;
  padding-bottom: 40%;
  width: 70%;
}

.live-v-dialog .content span {
  display: block;
  margin-bottom: 0.8rem;
  line-height: 0.533333rem;
  color: #ff5d5d;
}

[data-dpr="1"] .live-v-dialog .content span {
  font-size: 15px;
}

[data-dpr="2"] .live-v-dialog .content span {
  font-size: 30px;
}

[data-dpr="3"] .live-v-dialog .content span {
  font-size: 45px;
}