.short-knowledge-tip_FDGDS {
  display: inline-block;
  font-size: 0.32rem;
  font-family: PingFangSC-Semibold;
  font-weight: 600;
  color: white;
  line-height: normal;
}

.short-knowledge-tip_FDGDS .short-tip {
  position: relative;
}

.short-knowledge-tip_FDGDS .short-tip img {
  width: auto !important;
  vertical-align: middle;
  height: 0.853333rem;
}

.short-knowledge-tip_FDGDS .short-tip span {
  position: absolute;
  left: 0;
  top: 0.16rem;
  right: 0;
  bottom: 0;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
     -moz-box-pack: center;
          justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
     -moz-box-align: center;
          align-items: center;
}