.takeincome-record {
  height: 100%;
  -webkit-text-size-adjust: none;
          text-size-adjust: none;
}

.takeincome-record .scroller {
  -webkit-box-sizing: border-box;
     -moz-box-sizing: border-box;
          box-sizing: border-box;
  background: #fff;
}

.takeincome-record .scroller::before {
  content: "";
  display: block;
  background: #F2F2F2;
  height: 0.266667rem;
}

.takeincome-record .content {
  padding: 0.533333rem;
}

.takeincome-record .header-tip {
  line-height: 0.44rem;
  font-weight: 400;
  color: #999999;
  margin-bottom: 0.4rem;
}

[data-dpr="1"] .takeincome-record .header-tip {
  font-size: 12px;
}

[data-dpr="2"] .takeincome-record .header-tip {
  font-size: 24px;
}

[data-dpr="3"] .takeincome-record .header-tip {
  font-size: 36px;
}

.takeincome-record .record-item {
  padding: 0.4rem;
  background: #fff;
  -webkit-box-shadow: 0 0.053333rem 0.386667rem rgba(0, 0, 0, 0.07);
          box-shadow: 0 0.053333rem 0.386667rem rgba(0, 0, 0, 0.07);
  -webkit-border-radius: 0.426667rem;
          border-radius: 0.426667rem;
}

.takeincome-record .record-item:not(:last-child) {
  margin-bottom: 0.533333rem;
}

.takeincome-record .record-item p {
  line-height: 0.373333rem;
  color: #333333;
}

[data-dpr="1"] .takeincome-record .record-item p {
  font-size: 10px;
}

[data-dpr="2"] .takeincome-record .record-item p {
  font-size: 20px;
}

[data-dpr="3"] .takeincome-record .record-item p {
  font-size: 30px;
}

.takeincome-record .record-item p:not(:last-child) {
  margin-bottom: 0.026667rem;
}

.takeincome-record .record-item p .title {
  color: #999;
}

.takeincome-record .record-item p .desc.money {
  color: #F73657;
}

.takeincome-record .record-item p .desc.withdraw-status {
  font-weight: bold;
}

.takeincome-record .record-item p .desc.success {
  color: #00c353;
}

.takeincome-record .record-item p .desc.fail,
.takeincome-record .record-item p .desc.no_pass {
  color: #f73657;
}

.takeincome-record .record-item p .desc.pwaiting,
.takeincome-record .record-item p .desc.awating {
  color: #ff9f00;
}

.takeincome-record .btn-wrapper {
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
     -moz-box-pack: center;
          justify-content: center;
  padding-top: 0.2rem;
}

.takeincome-record .btn-wrapper .link-btn {
  display: block;
  text-align: center;
  width: 2.88rem;
  height: 0.8rem;
  line-height: 0.8rem;
  -webkit-border-radius: 0.4rem;
          border-radius: 0.4rem;
  -webkit-box-sizing: border-box;
     -moz-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0 0.4rem;
}

[data-dpr="1"] .takeincome-record .btn-wrapper .link-btn {
  font-size: 13px;
}

[data-dpr="2"] .takeincome-record .btn-wrapper .link-btn {
  font-size: 26px;
}

[data-dpr="3"] .takeincome-record .btn-wrapper .link-btn {
  font-size: 39px;
}

.takeincome-record .btn-wrapper .verify-again {
  color: #f73657;
  background: #fff;
}

[data-dpr="1"] .takeincome-record .btn-wrapper .verify-again {
  border: 1px solid #f73657;
}

[data-dpr="2"] .takeincome-record .btn-wrapper .verify-again {
  border: 2px solid #f73657;
}

[data-dpr="3"] .takeincome-record .btn-wrapper .verify-again {
  border: 3px solid #f73657;
}

.takeincome-record .btn-wrapper .send-again {
  color: #fff;
  background: #f73657;
}

.takeincome-record-resend-name {
  padding: 0 0.533333rem;
}

.takeincome-record-resend-name .resend-name-input {
  height: 0.853333rem;
  width: 100%;
}

[data-dpr="1"] .takeincome-record-resend-name .resend-name-input {
  border: 1px solid #ddd;
  font-size: 12px;
}

[data-dpr="2"] .takeincome-record-resend-name .resend-name-input {
  border: 2px solid #ddd;
  font-size: 24px;
}

[data-dpr="3"] .takeincome-record-resend-name .resend-name-input {
  border: 3px solid #ddd;
  font-size: 36px;
}