.__fold-view {
  position: relative;
  z-index: 0;
  padding-bottom: 2px;
}

.__fold-view .__fold-view-container {
  position: relative;
  padding-bottom: 1.066667rem;
}

.__fold-view .__fold-view-controller {
  position: absolute;
  z-index: 2;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1.066667rem;
  text-align: center;
  font-size: 0.373333rem;
  color: #999;
  overflow: hidden;
  cursor: pointer;
}

.__fold-view .__fold-view-controller p {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1.066667rem;
  line-height: 1.066667rem;
}

.__fold-view .__fold-view-controller i {
  display: inline-block;
  margin-left: .5em;
  -webkit-transition: -webkit-transform .3s;
  transition: -webkit-transform .3s;
  -moz-transition: transform .3s, -moz-transform .3s;
  transition: transform .3s;
  transition: transform .3s, -webkit-transform .3s, -moz-transform .3s;
  -webkit-transform-origin: 50% 50%;
     -moz-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
}

.__fold-view::after {
  content: "";
  position: absolute;
  z-index: 3;
  width: 100%;
  height: 3px;
  left: 0;
  bottom: 0;
  background-color: white;
}

.__fold-view.__fold-view-folded .__fold-view-container {
  max-height: 600px;
  overflow: hidden;
}

.__fold-view.__fold-view-folded .__fold-view-controller {
  height: 1.733333rem;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0)), color-stop(40%, rgba(255, 255, 255, 0.99)), color-stop(40%, #fff));
  background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.99) 40%, #fff);
  background: -moz-linear-gradient(top, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.99) 40%, #fff);
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.99) 40%, #fff);
}

.__fold-view.__fold-view-folded .__fold-view-controller i {
  -webkit-transform: rotate(180deg);
     -moz-transform: rotate(180deg);
          transform: rotate(180deg);
}

.__fold-view.__fold-view-disabled {
  padding-bottom: 0;
}

.__fold-view.__fold-view-disabled .__fold-view-container {
  padding-bottom: 0;
}

.__fold-view.__fold-view-disabled .__fold-view-controller {
  display: none;
}

.__fold-view.__fold-view-disabled::after {
  content: none;
}