.dialog-modal {
  z-index: 300;
}

.dialog-modal .co-dialog-content {
  line-height: 1.4;
}

.dialog-modal .co-dialog-content .co-dialog-title {
  color: #333;
  font-weight: bold;
  padding-top: 0.64rem;
  padding-bottom: 0.2rem;
}

[data-dpr="1"] .dialog-modal .co-dialog-content .co-dialog-title {
  font-size: 16px;
}

[data-dpr="2"] .dialog-modal .co-dialog-content .co-dialog-title {
  font-size: 32px;
}

[data-dpr="3"] .dialog-modal .co-dialog-content .co-dialog-title {
  font-size: 48px;
}

.dialog-modal .co-dialog-content > main {
  padding: 0;
}

[data-dpr="1"] .dialog-modal .co-dialog-content > main {
  font-size: 14px;
}

[data-dpr="2"] .dialog-modal .co-dialog-content > main {
  font-size: 28px;
}

[data-dpr="3"] .dialog-modal .co-dialog-content > main {
  font-size: 42px;
}

.dialog-modal .co-dialog-content > main .red {
  color: #F73657;
}

.dialog-modal .co-dialog-content .co-dialog-btn-line {
  border: 0;
  height: auto;
  min-height: auto;
  padding: 0.64rem 1.066667rem;
  margin-top: 0;
  cursor: auto;
}

.dialog-modal .co-dialog-content .co-dialog-btn-line > span {
  height: 1.013333rem;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  line-height: normal;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
     -moz-box-pack: center;
          justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
     -moz-box-align: center;
          align-items: center;
  -webkit-border-radius: 1.013333rem;
          border-radius: 1.013333rem;
  background-color: #F73657;
  color: #fff;
  margin-right: 0.4rem;
}

.dialog-modal .co-dialog-content .co-dialog-btn-line > span:last-child {
  margin-right: 0;
}

.dialog-modal .co-dialog-content .co-dialog-btn-line > span:nth-child(2)::before {
  content: none;
}