/**
 * 1px
 */

.top-tab-bar {
  position: relative;
  z-index: 1;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
  background: #ededed;
  border-bottom: 0.026667rem solid #E7E7E7;
}

.top-tab-bar .tab-item {
  position: relative;
  -webkit-box-flex: 1;
  -webkit-flex: 1;
     -moz-box-flex: 1;
          flex: 1;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
     -moz-box-pack: center;
          justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
     -moz-box-align: center;
          align-items: center;
  color: #666;
  cursor: pointer;
}

[data-dpr="1"] .top-tab-bar .tab-item {
  height: 44px;
  font-size: 14px;
  line-height: 44px;
}

[data-dpr="2"] .top-tab-bar .tab-item {
  height: 88px;
  font-size: 28px;
  line-height: 88px;
}

[data-dpr="3"] .top-tab-bar .tab-item {
  height: 132px;
  font-size: 42px;
  line-height: 132px;
}

.top-tab-bar .tab-item p {
  position: relative;
  height: 100%;
}

.top-tab-bar .tab-item p span {
  position: absolute;
  color: #fff;
  background-color: #F73657;
  top: 50%;
  left: 100%;
  -webkit-transform: translateY(-50%);
     -moz-transform: translateY(-50%);
          transform: translateY(-50%);
  white-space: nowrap;
  padding: 0 .4em;
  -webkit-box-sizing: border-box;
     -moz-box-sizing: border-box;
          box-sizing: border-box;
  text-align: center;
}

[data-dpr="1"] .top-tab-bar .tab-item p span {
  font-size: 11px;
  line-height: 16px;
  min-width: 16px;
  -webkit-border-radius: 16px;
          border-radius: 16px;
  margin-left: 8px;
}

[data-dpr="2"] .top-tab-bar .tab-item p span {
  font-size: 22px;
  line-height: 32px;
  min-width: 32px;
  -webkit-border-radius: 32px;
          border-radius: 32px;
  margin-left: 16px;
}

[data-dpr="3"] .top-tab-bar .tab-item p span {
  font-size: 33px;
  line-height: 48px;
  min-width: 48px;
  -webkit-border-radius: 48px;
          border-radius: 48px;
  margin-left: 24px;
}

.top-tab-bar .tab-item.active {
  color: #333;
  font-weight: bolder;
}

[data-dpr="1"] .top-tab-bar .tab-item.active {
  font-size: 16px;
}

[data-dpr="2"] .top-tab-bar .tab-item.active {
  font-size: 32px;
}

[data-dpr="3"] .top-tab-bar .tab-item.active {
  font-size: 48px;
}

.top-tab-bar .tab-item.active p::after {
  content: "";
  position: absolute;
  background-color: #F73657;
  left: 50%;
  -webkit-transform: translateX(-50%);
     -moz-transform: translateX(-50%);
          transform: translateX(-50%);
  bottom: 0;
  -webkit-border-radius: 0.133333rem;
          border-radius: 0.133333rem;
}

[data-dpr="1"] .top-tab-bar .tab-item.active p::after {
  width: 18px;
  height: 3px;
}

[data-dpr="2"] .top-tab-bar .tab-item.active p::after {
  width: 36px;
  height: 6px;
}

[data-dpr="3"] .top-tab-bar .tab-item.active p::after {
  width: 54px;
  height: 9px;
}

.top-tab-bar .tab-item::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  -webkit-transform-origin: 0 100%;
     -moz-transform-origin: 0 100%;
          transform-origin: 0 100%;
}

[data-dpr="1"] .top-tab-bar .tab-item::after {
  height: 0.5px;
}

[data-dpr="2"] .top-tab-bar .tab-item::after {
  height: 1px;
}

[data-dpr="3"] .top-tab-bar .tab-item::after {
  height: 1.5px;
}

@media (-webkit-min-device-pixel-ratio: 3.1) {
  .top-tab-bar .tab-item::after {
    -webkit-transform: scaleY(0.3);
       -moz-transform: scaleY(0.3);
            transform: scaleY(0.3);
  }
}