.try-listen-status-bar {
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
     -moz-box-align: center;
          align-items: center;
  padding: 0.16rem 0.466667rem;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
     -moz-box-pack: justify;
          justify-content: space-between;
  line-height: normal;
  background: #fff9e6;
}

.try-listen-status-bar .content {
  font-size: 0.373333rem;
  font-family: PingFangSC-Regular;
  font-weight: 400;
  color: #ff9f00;
  display: -webkit-inline-box;
  display: -webkit-inline-flex;
  display: -moz-inline-box;
  display: inline-flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
     -moz-box-align: center;
          align-items: center;
}

.try-listen-status-bar .content .icon-playing {
  width: 0.48rem;
  height: 0.48rem;
  margin-right: 0.133333rem;
}

.try-listen-status-bar .buy-button,
.try-listen-status-bar .invite-button {
  display: -webkit-inline-box;
  display: -webkit-inline-flex;
  display: -moz-inline-box;
  display: inline-flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
     -moz-box-align: center;
          align-items: center;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
     -moz-box-pack: center;
          justify-content: center;
  width: 2.4rem;
  height: 1.013333rem;
  background: #ffa30b;
  -webkit-border-radius: 0.133333rem;
          border-radius: 0.133333rem;
  font-size: 0.426667rem;
  font-family: PingFangSC-Regular;
  font-weight: 400;
  color: white;
}