.job-reminder-box {
  position: absolute;
  right: 0;
  z-index: 1;
  bottom: 2.4rem;
  background: #FFF9E6;
  height: 0.853333rem;
  padding: 0 0.4rem;
  -webkit-border-radius: 0.853333rem 0 0 0.853333rem;
          border-radius: 0.853333rem 0 0 0.853333rem;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
     -moz-box-align: center;
          align-items: center;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
     -moz-box-pack: center;
          justify-content: center;
  color: #FF9400;
  -webkit-transition: all 0.3s linear;
  -moz-transition: all 0.3s linear;
  transition: all 0.3s linear;
}

[data-dpr="1"] .job-reminder-box {
  width: 95px;
}

[data-dpr="2"] .job-reminder-box {
  width: 190px;
}

[data-dpr="3"] .job-reminder-box {
  width: 285px;
}

.job-reminder-box span {
  background: url(data:image/png;base64,) no-repeat left center;
  -webkit-background-size: 0.48rem auto;
          background-size: 0.48rem auto;
  padding-left: 0.56rem;
  height: 100%;
  width: 100%;
  line-height: 0.853333rem;
  -webkit-box-align: center;
  -webkit-align-items: center;
     -moz-box-align: center;
          align-items: center;
  overflow: hidden;
  display: inline-block;
  -webkit-transition: all 0.3s linear;
  -moz-transition: all 0.3s linear;
  transition: all 0.3s linear;
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
}

[data-dpr="1"] .job-reminder-box span {
  font-size: 13px;
}

[data-dpr="2"] .job-reminder-box span {
  font-size: 26px;
}

[data-dpr="3"] .job-reminder-box span {
  font-size: 39px;
}

.job-reminder-box.no-show {
  width: 0.213333rem;
  -webkit-transition: width 0.3s linear;
  -moz-transition: width 0.3s linear;
  transition: width 0.3s linear;
}

.job-reminder-box.no-show span {
  width: 0;
  overflow: hidden;
  -webkit-transition: all 0.3s linear;
  -moz-transition: all 0.3s linear;
  transition: all 0.3s linear;
}