.co-scroll-to-load {
  position: absolute;
  overflow-y: scroll;
  overflow-x: hidden;
  width: 100%;
  top: 0;
  bottom: 0;
  -webkit-overflow-scrolling: touch;
  padding-bottom: 1.6rem;
}

.co-scroll-to-load .list-loading,
.co-scroll-to-load .list-nomore {
  padding: 0.4rem 0.2rem;
  text-align: center;
  color: #999999;
}

[data-dpr="1"] .co-scroll-to-load .list-loading,
[data-dpr="1"] .co-scroll-to-load .list-nomore {
  font-size: 12px;
}

[data-dpr="2"] .co-scroll-to-load .list-loading,
[data-dpr="2"] .co-scroll-to-load .list-nomore {
  font-size: 24px;
}

[data-dpr="3"] .co-scroll-to-load .list-loading,
[data-dpr="3"] .co-scroll-to-load .list-nomore {
  font-size: 36px;
}

.co-scroll-to-load .list-loading {
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
     -moz-box-pack: center;
          justify-content: center;
}

.co-scroll-to-load .list-loading .dynamic-ellipsis {
  position: relative;
}

.co-scroll-to-load .list-loading .dynamic-ellipsis:after {
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  width: 100%;
  background: #fff;
  content: '';
  display: block;
  -webkit-animation: dynamic-ellipsis 1s infinite linear;
     -moz-animation: dynamic-ellipsis 1s infinite linear;
          animation: dynamic-ellipsis 1s infinite linear;
}

@-webkit-keyframes dynamic-ellipsis {
  0% {
    width: 100%;
  }

  1% {
    width: 50%;
  }

  33% {
    width: 50%;
  }

  34% {
    width: 0;
  }

  66% {
    width: 0;
  }

  67% {
    width: 100%;
  }

  100% {
    width: 100%;
  }
}

@-moz-keyframes dynamic-ellipsis {
  0% {
    width: 100%;
  }

  1% {
    width: 50%;
  }

  33% {
    width: 50%;
  }

  34% {
    width: 0;
  }

  66% {
    width: 0;
  }

  67% {
    width: 100%;
  }

  100% {
    width: 100%;
  }
}

@keyframes dynamic-ellipsis {
  0% {
    width: 100%;
  }

  1% {
    width: 50%;
  }

  33% {
    width: 50%;
  }

  34% {
    width: 0;
  }

  66% {
    width: 0;
  }

  67% {
    width: 100%;
  }

  100% {
    width: 100%;
  }
}

.co-scroll-to-load .need-space {
  height: 0.666667rem;
}