.app-download-bar-container {
  position: absolute;
  width: 50%;
  right: 0;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  -webkit-box-pack: end;
  -webkit-justify-content: flex-end;
     -moz-box-pack: end;
          justify-content: flex-end;
  z-index: 9;
  pointer-events: none;
}

.app-download-bar-container .app-download-bar {
  display: -webkit-inline-box;
  display: -webkit-inline-flex;
  display: -moz-inline-box;
  display: inline-flex;
  position: relative;
  -webkit-box-align: center;
  -webkit-align-items: center;
     -moz-box-align: center;
          align-items: center;
  position: relative;
  -webkit-border-top-left-radius: 0.666667rem;
          border-top-left-radius: 0.666667rem;
  -webkit-border-bottom-left-radius: 0.666667rem;
          border-bottom-left-radius: 0.666667rem;
  padding: 0 0.093333rem;
  pointer-events: auto;
  background-image: -webkit-linear-gradient(45deg, #f04d70 0%, #fd9a95 100%);
  background-image: -moz-linear-gradient(45deg, #f04d70 0%, #fd9a95 100%);
  background-image: linear-gradient(45deg, #f04d70 0%, #fd9a95 100%);
  -webkit-transition: 0.5s all;
  -moz-transition: 0.5s all;
  transition: 0.5s all;
}

[data-dpr="1"] .app-download-bar-container .app-download-bar {
  height: 23px;
  -webkit-transform: translateX(77.5px);
     -moz-transform: translateX(77.5px);
          transform: translateX(77.5px);
}

[data-dpr="2"] .app-download-bar-container .app-download-bar {
  height: 46px;
  -webkit-transform: translateX(155px);
     -moz-transform: translateX(155px);
          transform: translateX(155px);
}

[data-dpr="3"] .app-download-bar-container .app-download-bar {
  height: 69px;
  -webkit-transform: translateX(232.5px);
     -moz-transform: translateX(232.5px);
          transform: translateX(232.5px);
}

.app-download-bar-container .app-download-bar img.pop-out {
  display: inline-block;
  width: 0.453333rem;
  height: 0.453333rem;
  cursor: pointer;
}

.app-download-bar-container .app-download-bar span.pop-text {
  color: #ffffff;
  margin-left: 0.133333rem;
  white-space: nowrap;
  cursor: pointer;
}

[data-dpr="1"] .app-download-bar-container .app-download-bar span.pop-text {
  font-size: 12px;
  width: 72.5px;
}

[data-dpr="2"] .app-download-bar-container .app-download-bar span.pop-text {
  font-size: 24px;
  width: 145px;
}

[data-dpr="3"] .app-download-bar-container .app-download-bar span.pop-text {
  font-size: 36px;
  width: 217.5px;
}

.app-download-bar-container .app-download-bar.active {
  -webkit-transform: translateX(0);
     -moz-transform: translateX(0);
          transform: translateX(0);
}

.app-download-bar-container.mid {
  top: 1.333333rem;
}

.app-download-bar-container.top {
  top: 0.2rem;
}

.app-download-bar-container.bottom {
  top: 6.266667rem;
}