.flag-tab-box {
  position: absolute;
  z-index: 10;
  font-size: 0;
  margin: 0 !important;
  padding: 0 !important;
}

[data-dpr="1"] .flag-tab-box {
  top: 0.5px;
  left: 0.5px;
}

[data-dpr="2"] .flag-tab-box {
  top: 1px;
  left: 1px;
}

[data-dpr="3"] .flag-tab-box {
  top: 1.5px;
  left: 1.5px;
}

.flag-tab-box .flag-txt {
  position: relative;
  background: #F73657;
  -webkit-box-sizing: border-box;
     -moz-box-sizing: border-box;
          box-sizing: border-box;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  line-height: 1.1;
  -webkit-box-align: center;
  -webkit-align-items: center;
     -moz-box-align: center;
          align-items: center;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
     -moz-box-pack: center;
          justify-content: center;
  padding: 0.066667rem;
  font-weight: 500;
  color: #fff;
  text-align: center;
  -webkit-border-radius: 0.053333rem;
          border-radius: 0.053333rem;
}

[data-dpr="1"] .flag-tab-box .flag-txt {
  font-size: 8px;
}

[data-dpr="2"] .flag-tab-box .flag-txt {
  font-size: 16px;
}

[data-dpr="3"] .flag-tab-box .flag-txt {
  font-size: 24px;
}