.full-width-btn {
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
     -moz-box-align: center;
          align-items: center;
  width: 100%;
  line-height: 1.066667rem;
}

[data-dpr="1"] .full-width-btn {
  height: 40px;
}

[data-dpr="2"] .full-width-btn {
  height: 80px;
}

[data-dpr="3"] .full-width-btn {
  height: 120px;
}

.full-width-btn .txt {
  -webkit-box-flex: 1;
  -webkit-flex: 1 1;
     -moz-box-flex: 1;
          flex: 1 1;
  padding-left: 0.533333rem;
  text-align: center;
}

.full-width-btn .btn-close {
  -webkit-box-flex: 0;
  -webkit-flex: 0 0;
     -moz-box-flex: 0;
          flex: 0 0;
  margin-right: 0.133333rem;
  padding: 0.053333rem;
  border: 0.026667rem #666 solid;
  -webkit-border-radius: 0.4rem;
          border-radius: 0.4rem;
  font-size: 0.24rem;
}