.archivementcard-btn {
  width: 1.493333rem;
  height: 0.906667rem;
  margin: 0.133333rem 0;
  cursor: pointer;
  -webkit-box-sizing: border-box;
     -moz-box-sizing: border-box;
          box-sizing: border-box;
  pointer-events: auto;
  background: url(/wechat-react/img/achievement_card_logo.3abcaf44ee3d6478887a2810a011ebdc.png) no-repeat center center;
  -webkit-background-size: 98% 98%;
          background-size: 98%;
  -webkit-transform: rotate(-8deg);
     -moz-transform: rotate(-8deg);
          transform: rotate(-8deg);
}

.audio-grapic .archivementcard-btn,
.thousand-live-video .archivementcard-btn {
  position: absolute;
  right: 0.266667rem;
  bottom: 2rem;
  z-index: 2;
}

.archivementcard-modal {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 999999;
  background-color: rgba(0, 0, 0, 0.7);
}

.archivementcard-modal .archivementcard-wrap {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 8rem;
  padding: 0.4rem;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
     -moz-box-orient: vertical;
     -moz-box-direction: normal;
          flex-direction: column;
  -webkit-box-sizing: border-box;
     -moz-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-border-radius: 0.2rem;
          border-radius: 0.2rem;
  text-align: center;
  -webkit-transform: translate(-50%, -50%);
     -moz-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: #fff;
}

.archivementcard-modal .close-modal-wrap {
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
     -moz-box-orient: horizontal;
     -moz-box-direction: normal;
