  width: 0.573333rem;
  height: 0.506667rem;
  background: url(data:image/png;base64,) no-repeat center center;
  -webkit-background-size: 100% 100%;
          background-size: 100%;
}

.archivementcard-modal .archivementcard-badge {
  height: 3.466667rem;
  margin: 0.133333rem 0;
  background: url(/wechat-react/img/achivement_card_badge.dc00cb3b335385c7814feb906de8569d.png) no-repeat center center;
  -webkit-background-size: 60% 60%;
          background-size: 60%;
}

.archivementcard-modal .archivementcard-text {
  color: #666666;
  font-size: 0.373333rem;
  line-height: 0.6rem;
