          border-bottom-left-radius: 0.666667rem;
  -webkit-transition: all;
  -moz-transition: all;
  transition: all;
  -webkit-animation: moveTop 1s ease-out 2s both;
     -moz-animation: moveTop 1s ease-out 2s both;
          animation: moveTop 1s ease-out 2s both;
}

.btn-home-float.top::before {
  content: '';
  display: inline-block;
  height: 0.333333rem;
  width: 0.333333rem;
  margin-right: 0.2rem;
  background: url(data:image/png;base64,) center center no-repeat;
