.community-suspension {
  cursor: pointer;
  width: 1.066667rem;
  height: 1.066667rem;
  position: absolute;
  right: 0.533333rem;
  bottom: 3.2rem;
  -webkit-border-radius: 50%;
          border-radius: 50%;
  -webkit-box-shadow: 0 0.026667rem 0.186667rem 0 rgba(0, 0, 0, 0.2);
          box-shadow: 0 0.026667rem 0.186667rem 0 rgba(0, 0, 0, 0.2);
  background: #fff url(data:image/png;base64,) center/0.533333rem no-repeat;
}

.community-suspension.member-up {
  bottom: 3.733333rem;
}

.community-suspension .tooltip {
  opacity: 0;
  white-space: nowrap;
  padding: 0.24rem 0.266667rem;
  background-color: #666666;
  color: #fff;
  line-height: 1.5;
  -webkit-border-radius: 0.133333rem;
          border-radius: 0.133333rem;
  font-weight: bold;
  position: absolute;
  left: 0;
  top: 50%;
  text-align: center;
  -webkit-box-sizing: border-box;
     -moz-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-transition: opacity linear 0.8s;
  -moz-transition: opacity linear 0.8s;
  transition: opacity linear 0.8s;
  -webkit-transform: translate(-108%, -50%);
     -moz-transform: translate(-108%, -50%);
          transform: translate(-108%, -50%);
}

[data-dpr="1"] .community-suspension .tooltip {
  font-size: 12px;
}

[data-dpr="2"] .community-suspension .tooltip {
  font-size: 24px;
}

[data-dpr="3"] .community-suspension .tooltip {
  font-size: 36px;
}

.community-suspension .tooltip.visible-tooltip {
  opacity: 1;
}

.community-suspension .tooltip::after {
  content: "";
  display: block;
  width: 0.266667rem;
  height: 0.266667rem;
  -webkit-border-radius: 0 0.08rem 0 0;
          border-radius: 0 0.08rem 0 0;
  background-color: #666;
  position: absolute;
  right: 0;
  top: 50%;
  -webkit-transform: translate(50%, -50%) rotate(45deg);
     -moz-transform: translate(50%, -50%) rotate(45deg);
          transform: translate(50%, -50%) rotate(45deg);
}