.pyramid-dialog .pyramid-wrap {
  display: block;
  text-align: center;
  padding: 1.066667rem 0 0.8rem;
}

.pyramid-dialog .logo-pyramid {
  display: inline-block;
  width: 4rem;
}

.pyramid-dialog .title {
  display: block;
  text-align: center;
  color: #575757;
  line-height: 0.666667rem;
  font-weight: bold;
  margin-top: 0.533333rem;
}

[data-dpr="1"] .pyramid-dialog .title {
  font-size: 20px;
}

[data-dpr="2"] .pyramid-dialog .title {
  font-size: 40px;
}

[data-dpr="3"] .pyramid-dialog .title {
  font-size: 60px;
}

.pyramid-dialog .descript {
  color: #9b9ea3;
  display: block;
  text-align: center;
  line-height: 0.4rem;
  margin-top: 0.266667rem;
}

[data-dpr="1"] .pyramid-dialog .descript {
  font-size: 14px;
}

[data-dpr="2"] .pyramid-dialog .descript {
  font-size: 28px;
}

[data-dpr="3"] .pyramid-dialog .descript {
  font-size: 42px;
}