.job-list-dialog {
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
     -moz-box-orient: vertical;
     -moz-box-direction: normal;
          flex-direction: column;
  background-color: #fff;
}

.job-list-dialog > header {
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
  position: relative;
  text-align: center;
  color: #333;
  font-weight: 600;
  border-bottom: 1px solid #EEE;
}

[data-dpr="1"] .job-list-dialog > header {
  height: 44px;
  line-height: 44px;
  font-size: 16px;
}

[data-dpr="2"] .job-list-dialog > header {
  height: 88px;
  line-height: 88px;
  font-size: 32px;
}

[data-dpr="3"] .job-list-dialog > header {
  height: 132px;
  line-height: 132px;
  font-size: 48px;
}

.job-list-dialog > header > i {
  position: absolute;
  padding: 0 0.4rem;
  right: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
     -moz-transform: translateY(-50%);
          transform: translateY(-50%);
  color: #333;
}

[data-dpr="1"] .job-list-dialog > header > i {
  font-size: 16px;
}

[data-dpr="2"] .job-list-dialog > header > i {
  font-size: 32px;
}

[data-dpr="3"] .job-list-dialog > header > i {
  font-size: 48px;
}

.job-list-dialog > .job-scroll-box {
  position: relative;
  padding: 0.266667rem 0;
  width: 100%;
  overflow-x: hidden;
  overflow-y: scroll;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}

.job-list-dialog > .job-scroll-box .job-item {
  padding: 0 0.533333rem;
  height: 1.066667rem;
  line-height: 1.066667rem;
  color: #333;
  white-space: nowrap;
  overflow: hidden;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
     -moz-box-align: center;
          align-items: center;
}

[data-dpr="1"] .job-list-dialog > .job-scroll-box .job-item {
  font-size: 14px;
}

[data-dpr="2"] .job-list-dialog > .job-scroll-box .job-item {
  font-size: 28px;
}

[data-dpr="3"] .job-list-dialog > .job-scroll-box .job-item {
  font-size: 42px;
}

.job-list-dialog > .job-scroll-box .job-item .title {
  -webkit-box-flex: 1;
  -webkit-flex-grow: 1;
     -moz-box-flex: 1;
          flex-grow: 1;
  overflow: hidden;
  text-overflow: ellipsis;
}

.job-list-dialog > .job-scroll-box .job-item .status {
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
  height: 0.733333rem;
  -webkit-border-radius: 0.733333rem;
          border-radius: 0.733333rem;
  line-height: normal;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
     -moz-box-align: center;
          align-items: center;
  padding: 0 1em;
  font-size: 0.373333rem;
  color: #fff;
  background-color: #f73657;
  margin-left: 1em;
}

.job-list-dialog > .job-scroll-box .job-item.complete {
  color: #999;
}

.job-list-dialog > .job-scroll-box .job-item.complete .status {
  background-color: #eee;
  color: #999;
}

.job-list-dialog > .job-scroll-box .job-item.select {
  background: #F7F7F7;
}

.job-list-dialog-box .co-dialog-bottom,
.job-list-dialog {
  -webkit-border-radius: 0.373333rem 0.373333rem 0 0;
          border-radius: 0.373333rem 0.373333rem 0 0;
}

.job-list-dialog-box .co-dialog-bottom {
  height: 60% !important;
  background: #fff;
}