.co-score-star {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  white-space: nowrap;
  height: 1em;
  overflow-y: hidden;
}

.co-score-star i {
  display: inline-block;
  width: 1em;
  height: 1em;
  background-repeat: no-repeat;
  background-position: left top;
  -webkit-background-size: 1em auto;
          background-size: 1em auto;
  vertical-align: top;
  border-left: 0.053333rem solid transparent;
  border-right: 0.053333rem solid transparent;
}

.co-score-star i:first-child {
  border-left: none;
}

.co-score-star i:last-child {
  border-right: none;
}

.co-score-star.editing i {
  cursor: pointer;
}

.co-score-star.editing:hover i {
  background-image: url(https://img.qlchat.com/qlLive/liveComment/UQ76YYZI-275H-XAJU-1535626137079-3OR46OV33VO9.png);
}

.co-score-star.editing:hover i:hover ~ i {
  background-image: url(https://img.qlchat.com/qlLive/liveComment/YQG7GJJI-6E6S-RAVY-1535626147354-13K69KABT6DS.png);
}

.co-score-star.editing:hover .star-real {
  display: none;
}

.co-score-star .star-bg i {
  background-image: url(https://img.qlchat.com/qlLive/liveComment/YQG7GJJI-6E6S-RAVY-1535626147354-13K69KABT6DS.png);
}

.co-score-star .star-real {
  position: absolute;
  top: 0;
  left: 0;
}

.co-score-star .star-real i {
  background-image: url(https://img.qlchat.com/qlLive/liveComment/UQ76YYZI-275H-XAJU-1535626137079-3OR46OV33VO9.png);
}