.topic-assemble-content {
  padding: 0.266667rem 0;
  position: relative;
}

.topic-assemble-content div[class^="content-"] {
  padding: 0.266667rem 0;
  line-height: 2;
  color: #333;
  letter-spacing: 0.026667rem;
}

[data-dpr="1"] .topic-assemble-content div[class^="content-"] {
  font-size: 15px;
}

[data-dpr="2"] .topic-assemble-content div[class^="content-"] {
  font-size: 30px;
}

[data-dpr="3"] .topic-assemble-content div[class^="content-"] {
  font-size: 45px;
}

.topic-assemble-content .content-title {
  margin-top: 0.533333rem;
  font-weight: bold;
  line-height: 1 !important;
}

[data-dpr="1"] .topic-assemble-content .content-title {
  font-size: 18px !important;
}

[data-dpr="2"] .topic-assemble-content .content-title {
  font-size: 36px !important;
}

[data-dpr="3"] .topic-assemble-content .content-title {
  font-size: 54px !important;
}

.topic-assemble-content .content-title:first-of-type {
  margin-top: 0;
}

.topic-assemble-content .content-txet {
  text-align: justify !important;
}

.topic-assemble-content .content-img img {
  width: 100%;
  height: auto;
  max-width: 100%;
}

.topic-assemble-content .empty {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
     -moz-box-orient: vertical;
     -moz-box-direction: normal;
          flex-direction: column;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
     -moz-box-pack: center;
          justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
     -moz-box-align: center;
          align-items: center;
}

.topic-assemble-content .empty .empty-poster {
  width: 2.56rem;
  height: 3.626667rem;
  background: url(/wechat-react/img/empty.cccac624e03e35b567e7520b615c925d.png) no-repeat center;
  -webkit-background-size: contain;
          background-size: contain;
}

.topic-assemble-content .empty .empty-text {
  margin-top: 0.373333rem;
  color: #999;
}

[data-dpr="1"] .topic-assemble-content .empty .empty-text {
  font-size: 12px;
}

[data-dpr="2"] .topic-assemble-content .empty .empty-text {
  font-size: 24px;
}

[data-dpr="3"] .topic-assemble-content .empty .empty-text {
  font-size: 36px;
}