.protocol-page .title {
  color: #575757;
  margin-bottom: 0.266667rem;
  font-weight: bold;
  text-align: center;
  line-height: 0.666667rem;
}

[data-dpr="1"] .protocol-page .title {
  font-size: 16px;
}

[data-dpr="2"] .protocol-page .title {
  font-size: 32px;
}

[data-dpr="3"] .protocol-page .title {
  font-size: 48px;
}

.protocol-page p {
  padding: 0.066667rem 0 0.133333rem;
}

[data-dpr="1"] .protocol-page p {
  line-height: 15px;
  font-size: 10px;
}

[data-dpr="2"] .protocol-page p {
  line-height: 30px;
  font-size: 20px;
}

[data-dpr="3"] .protocol-page p {
  line-height: 45px;
  font-size: 30px;
}