.ui-action-sheet {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 99;
  background: rgba(0, 0, 0, 0.5);
  display: none;
}

.ui-action-sheet.show {
  display: block;
}

.ui-action-sheet .container-tt {
  background: #f5f5f5;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}

.ui-action-sheet .container-tt .sheet-list {
  margin-bottom: 0.24rem;
  background: #fff;
}

.ui-action-sheet .container-tt .sheet-list .sheet-item {
  height: 1.333333rem;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
     -moz-box-align: center;
          align-items: center;
  padding: 0 0.4rem;
  border-bottom: 1px solid #e2e2e2;
  cursor: pointer;
}

[data-dpr="1"] .ui-action-sheet .container-tt .sheet-list .sheet-item {
  font-size: 15px;
}

[data-dpr="2"] .ui-action-sheet .container-tt .sheet-list .sheet-item {
  font-size: 30px;
}

[data-dpr="3"] .ui-action-sheet .container-tt .sheet-list .sheet-item {
  font-size: 45px;
}

.ui-action-sheet .container-tt .sheet-list .sheet-item .strong {
  color: #f73657;
}

.ui-action-sheet .container-tt .sheet-list .sheet-item:last-child {
  border-bottom: 0;
}

.ui-action-sheet .container-tt .cancel-btn {
  height: 1.333333rem;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  line-height: normal;
  -webkit-box-align: center;
  -webkit-align-items: center;
     -moz-box-align: center;
          align-items: center;
  line-height: normal;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
     -moz-box-pack: center;
          justify-content: center;
  cursor: pointer;
  background: #fff;
}

[data-dpr="1"] .ui-action-sheet .container-tt .cancel-btn {
  font-size: 16px;
}

[data-dpr="2"] .ui-action-sheet .container-tt .cancel-btn {
  font-size: 32px;
}

[data-dpr="3"] .ui-action-sheet .container-tt .cancel-btn {
  font-size: 48px;
}