.ct-tag-box {
  height: 1.173333rem;
  overflow: hidden;
  -webkit-box-shadow: 0px -2px 0px 0px #f7f7f7;
          box-shadow: 0px -2px 0px 0px #f7f7f7;
  border-bottom: 2px solid #F7F7F7;
}

.ct-tag-box .ct-tag-list {
  display: -webkit-box;
  overflow-x: auto;
  scroll-behavior: smooth;
  height: 1.333333rem;
  -webkit-box-sizing: border-box;
     -moz-box-sizing: border-box;
          box-sizing: border-box;
}

.ct-tag-box .ct-tag-list p {
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  padding: 0 0.32rem;
  height: 1.173333rem;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
     -moz-box-pack: center;
          justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
     -moz-box-align: center;
          align-items: center;
  color: #666;
  position: relative;
}

[data-dpr="1"] .ct-tag-box .ct-tag-list p {
  font-size: 14px;
}

[data-dpr="2"] .ct-tag-box .ct-tag-list p {
  font-size: 28px;
}

[data-dpr="3"] .ct-tag-box .ct-tag-list p {
  font-size: 42px;
}

.ct-tag-box .ct-tag-list p:first-child,
.ct-tag-box .ct-tag-list p:last-child {
  padding: 0 0.32rem 0 0.426667rem;
}

.ct-tag-box .ct-tag-list p.action {
  color: #333;
  font-weight: bold;
}

[data-dpr="1"] .ct-tag-box .ct-tag-list p.action {
  font-size: 18px;
}

[data-dpr="2"] .ct-tag-box .ct-tag-list p.action {
  font-size: 36px;
}

[data-dpr="3"] .ct-tag-box .ct-tag-list p.action {
  font-size: 54px;
}

.ct-tag-box .ct-tag-list p.action::before {
  position: absolute;
  width: 0.48rem;
  height: 0.08rem;
  -webkit-border-radius: 0.08rem;
          border-radius: 0.08rem;
  left: 50%;
  bottom: 0.053333rem;
  -webkit-transform: translateX(-50%);
     -moz-transform: translateX(-50%);
          transform: translateX(-50%);
  background: #E0414F;
  content: '';
}