.tips-card {
  position: relative;
  display: block;
  margin: 0.266667rem;
  padding: 0 0.4rem 0.4rem 0.4rem;
}

.tips-card .title {
  display: block;
  height: 0.8rem;
  line-height: 0.8rem;
  color: #999;
}

[data-dpr="1"] .tips-card .title {
  font-size: 14px;
}

[data-dpr="2"] .tips-card .title {
  font-size: 28px;
}

[data-dpr="3"] .tips-card .title {
  font-size: 42px;
}

.tips-card li {
  list-style-type: decimal;
  line-height: 1.4;
  padding: 0 0.133333rem 0 0.053333rem;
  margin-left: 0.533333rem;
  color: #999;
}

[data-dpr="1"] .tips-card li {
  font-size: 12px;
}

[data-dpr="2"] .tips-card li {
  font-size: 24px;
}

[data-dpr="3"] .tips-card li {
  font-size: 36px;
}

.tips-card li .btn-create-live-enter {
  color: #F73657;
}