.ql-calendar-card .qcc-head {
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
}

.ql-calendar-card .qcc-head .qcc-item {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
     -moz-box-flex: 1;
          flex: 1;
  text-align: center;
  line-height: 0.906667rem;
  font-size: 0.373333rem;
  color: #666666;
  line-height: 0.533333rem;
}

.ql-calendar-card .qcc-date::after {
  content: '';
  display: table;
  clear: both;
}

.ql-calendar-card .qcc-date .qcd-item {
  float: left;
  width: 14.28571%;
  line-height: 0.906667rem;
  color: #666666;
  text-align: center;
  margin-top: 0.373333rem;
}

.ql-calendar-card .qcc-date .qcd-item .qcd-on {
  position: relative;
  width: 0.906667rem;
  height: 0.906667rem;
  margin: 0 auto;
}

.ql-calendar-card .qcc-date .qcd-item .qcd-on span {
  display: inline-block;
  -webkit-border-radius: 50%;
          border-radius: 50%;
  color: white;
  width: 100%;
  height: 100;
  background: #e0414f;
  -webkit-box-sizing: border-box;
     -moz-box-sizing: border-box;
          box-sizing: border-box;
}

.ql-calendar-card .qcc-date .qcd-item .play span {
  background: #f7f7f7;
  color: #666666;
}

.ql-calendar-card .qcc-date .qcd-item .success i {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 0.32rem;
  height: 0.32rem;
  background: url("https://img.qlchat.com/qlLive/business/FLKMFZSJ-OBYF-WDB1-1558778300214-MIZ7X559FFL4.png") no-repeat 0 0;
  -webkit-background-size: 100% 100%;
          background-size: 100%;
}

.ql-calendar-card .qcc-date .qcd-item .fail span {
  background: #f7f7f7;
  color: #e0414f;
}

.ql-calendar-card .qcc-date .qcd-item .now span {
  background: none;
  border: 0.026667rem solid #e0414f;
  color: #e0414f;
  line-height: 0.866667rem;
}