.company-box {
  padding: 0.653333rem 1.08rem;
}

.company-box .real-head {
  background: url(/wechat-react/img/ec.f410e2427e8e67af235bed903940c392.png) no-repeat center top;
  padding-top: 2.4rem;
  -webkit-background-size: 2.146667rem 2.146667rem;
          background-size: 2.146667rem;
  color: #333;
  margin-bottom: 0.6rem;
}

[data-dpr="1"] .company-box .real-head {
  font-size: 16px;
}

[data-dpr="2"] .company-box .real-head {
  font-size: 32px;
}

[data-dpr="3"] .company-box .real-head {
  font-size: 48px;
}

.company-box p {
  color: #999;
  line-height: 1.4;
  text-align: left;
}

[data-dpr="1"] .company-box p {
  font-size: 14px;
}

[data-dpr="2"] .company-box p {
  font-size: 28px;
}

[data-dpr="3"] .company-box p {
  font-size: 42px;
}