.symbol-list i {
  display: inline-block;
  margin-left: 0.08rem;
  vertical-align: middle;
  cursor: pointer;
}

[data-dpr="1"] .symbol-list i {
  width: 25px;
  height: 30px;
}

[data-dpr="2"] .symbol-list i {
  width: 50px;
  height: 60px;
}

[data-dpr="3"] .symbol-list i {
  width: 75px;
  height: 90px;
}

