.books-topic-item {
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  padding: 0.426667rem;
}

.books-topic-item .books-img {
  width: 2.16rem;
  height: 2.76rem;
  overflow: hidden;
  margin-right: 0.266667rem;
  -webkit-border-radius: 0.08rem;
          border-radius: 0.08rem;
}

.books-topic-item .books-img .books-pic {
  width: 2.16rem;
  height: 2.76rem;
}

.books-topic-item .books-img img {
  width: 100%;
  height: auto;
}

.books-topic-item .books-intro {
  -webkit-box-sizing: border-box;
     -moz-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-box-flex: 1;
  -webkit-flex-grow: 1;
     -moz-box-flex: 1;
          flex-grow: 1;
  -webkit-flex-basis: 0;
          flex-basis: 0;
  max-width: 100%;
  position: relative;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
     -moz-box-align: center;
          align-items: center;
}

.books-topic-item .books-intro > div {
  position: absolute;
  width: 100%;
}

.books-topic-item .books-intro > div h3 {
  color: #333;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  line-height: normal;
  padding: 0.08rem 0 0.16rem;
}

[data-dpr="1"] .books-topic-item .books-intro > div h3 {
  font-size: 15px;
}

[data-dpr="2"] .books-topic-item .books-intro > div h3 {
  font-size: 30px;
}

[data-dpr="3"] .books-topic-item .books-intro > div h3 {
  font-size: 45px;
}

.books-topic-item .books-intro > div h3.books-name {
  padding-bottom: 0.333333rem;
}

.books-topic-item .books-intro > div p {
  color: #666;
  line-height: 1.5;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  margin-bottom: 0.373333rem;
}

[data-dpr="1"] .books-topic-item .books-intro > div p {
  font-size: 13px;
  height: 37px;
}

[data-dpr="2"] .books-topic-item .books-intro > div p {
  font-size: 26px;
  height: 74px;
}

[data-dpr="3"] .books-topic-item .books-intro > div p {
  font-size: 39px;
  height: 111px;
}

.books-topic-item .books-intro > div .books-decs {
  position: relative;
  color: #999;
}

[data-dpr="1"] .books-topic-item .books-intro > div .books-decs {
  font-size: 11px;
}

[data-dpr="2"] .books-topic-item .books-intro > div .books-decs {
  font-size: 22px;
}

[data-dpr="3"] .books-topic-item .books-intro > div .books-decs {
  font-size: 33px;
}