.hot-heart-dialog {
  text-align: center;
}

.hot-heart-dialog b {
  color: #575757;
  display: block;
  margin-top: 0.8rem;
  margin-bottom: 0.4rem;
}

[data-dpr="1"] .hot-heart-dialog b {
  font-size: 17px;
}

[data-dpr="2"] .hot-heart-dialog b {
  font-size: 34px;
}

[data-dpr="3"] .hot-heart-dialog b {
  font-size: 51px;
}

.hot-heart-dialog .co-dialog-close {
  top: 0.133333rem;
  -webkit-border-radius: 0.266667rem;
          border-radius: 0.266667rem;
  background-color: rgba(0, 0, 0, 0.2);
  color: #fff;
}

[data-dpr="1"] .hot-heart-dialog .co-dialog-close {
  width: 20px;
  height: 20px;
  line-height: 19px;
}

[data-dpr="2"] .hot-heart-dialog .co-dialog-close {
  width: 40px;
  height: 40px;
  line-height: 38px;
}

[data-dpr="3"] .hot-heart-dialog .co-dialog-close {
  width: 60px;
  height: 60px;
  line-height: 57px;
}

.hot-heart-dialog .content:before {
  content: '';
  display: block;
  background: url(/wechat-react/img/hot-box.d3c779de1851340b6a8c82203a046720.png) center center no-repeat;
  -webkit-background-size: 100% auto;
          background-size: 100% auto;
  padding-bottom: 72%;
  width: 100%;
}

.hot-heart-dialog .content span {
  display: block;
  margin-bottom: 0.8rem;
  line-height: 0.533333rem;
  color: #9b9ea3;
}

[data-dpr="1"] .hot-heart-dialog .content span {
  font-size: 15px;
}

[data-dpr="2"] .hot-heart-dialog .content span {
  font-size: 30px;
}

[data-dpr="3"] .hot-heart-dialog .content span {
  font-size: 45px;
}