.push-course-dialog {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
     -moz-box-pack: center;
          justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
     -moz-box-align: center;
          align-items: center;
  background: rgba(0, 0, 0, 0.5);
  z-index: 100;
  line-height: 1.2;
}

[data-dpr="1"] .push-course-dialog {
  font-size: 14px;
}

[data-dpr="2"] .push-course-dialog {
  font-size: 28px;
}

[data-dpr="3"] .push-course-dialog {
  font-size: 42px;
}

.push-course-dialog .container {
  width: 9.2rem;
  -webkit-border-radius: 0.2rem;
          border-radius: 0.2rem;
  overflow: hidden;
  background: #fff;
}

.push-course-dialog .container .main {
  padding: 0.533333rem 0.533333rem;
}

.push-course-dialog .container .main header {
  text-align: center;
  color: #313131;
  font-weight: 700;
  margin-bottom: 0.4rem;
}

[data-dpr="1"] .push-course-dialog .container .main header {
  font-size: 17px;
}

[data-dpr="2"] .push-course-dialog .container .main header {
  font-size: 34px;
}

[data-dpr="3"] .push-course-dialog .container .main header {
  font-size: 51px;
}

.push-course-dialog .container .main header var {
  color: #f62f4d;
}

.push-course-dialog .container .main .select_ul {
  border-bottom: solid 1px #f8f4ed;
  padding: 0.266667rem 0;
}

.push-course-dialog .container .main .select_ul li {
  line-height: 0.533333rem;
  color: #424242;
  padding: 0.133333rem 0;
  cursor: pointer;
  display: block;
  position: relative;
  padding: 0.133333rem 0 0.133333rem 0.533333rem;
}

[data-dpr="1"] .push-course-dialog .container .main .select_ul li {
  font-size: 13px;
}

[data-dpr="2"] .push-course-dialog .container .main .select_ul li {
  font-size: 26px;
}

[data-dpr="3"] .push-course-dialog .container .main .select_ul li {
  font-size: 39px;
}

.push-course-dialog .container .main .select_ul li b {
  color: #424242;
}

.push-course-dialog .container .main .select_ul li i {
  font-style: normal;
  color: #999999;
}

.push-course-dialog .container .main .select_ul li .new::after {
  content: 'new';
  color: white;
  background-color: #f62f4d;
  -webkit-border-radius: 0.026667rem;
          border-radius: 0.026667rem;
  margin-left: 0.106667rem;
  padding: 0.026667rem 0.053333rem;
}

[data-dpr="1"] .push-course-dialog .container .main .select_ul li .new::after {
  font-size: 10px;
}

[data-dpr="2"] .push-course-dialog .container .main .select_ul li .new::after {
  font-size: 20px;
}

[data-dpr="3"] .push-course-dialog .container .main .select_ul li .new::after {
  font-size: 30px;
}

.push-course-dialog .container .main .select_ul li .tips {
  display: block;
  color: #f85973;
}

[data-dpr="1"] .push-course-dialog .container .main .select_ul li .tips {
  font-size: 12px;
}

[data-dpr="2"] .push-course-dialog .container .main .select_ul li .tips {
  font-size: 24px;
}

[data-dpr="3"] .push-course-dialog .container .main .select_ul li .tips {
  font-size: 36px;
}

.push-course-dialog .container .main .select_ul li .checkbox {
  display: inline-block;
  -webkit-border-radius: 0.026667rem;
          border-radius: 0.026667rem;
  text-align: center;
  line-height: 0.266667rem;
  vertical-align: top;
  margin: 0.106667rem 0.173333rem 0 -0.506667rem;
  color: #807878;
  position: relative;
}

[data-dpr="1"] .push-course-dialog .container .main .select_ul li .checkbox {
  width: 10px;
  height: 10px;
  border: solid 1px #999999;
  font-size: 10px;
}

[data-dpr="2"] .push-course-dialog .container .main .select_ul li .checkbox {
  width: 20px;
  height: 20px;
  border: solid 2px #999999;
  font-size: 20px;
}

[data-dpr="3"] .push-course-dialog .container .main .select_ul li .checkbox {
  width: 30px;
  height: 30px;
  border: solid 3px #999999;
  font-size: 30px;
}

.push-course-dialog .container .main .select_ul li .checkbox::before {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
     -moz-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.push-course-dialog .container .main .select_ul li.disable {
  color: #b3b3b3 !important;
}

.push-course-dialog .container .main .select_ul li.disable b,
.push-course-dialog .container .main .select_ul li.disable i {
  color: #b3b3b3 !important;
}

.push-course-dialog .container .main .select_ul li.disable .checkbox {
  border-color: #d6d6d6;
}

.push-course-dialog .container .main .select_ul li.disable .checkbox::before {
  display: none;
  content: '';
}

.push-course-dialog .container .main .select_ul li .goto-tuto {
  width: 0.666667rem;
  height: 0.666667rem;
  display: block;
  position: absolute;
  right: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
     -moz-transform: translateY(-50%);
          transform: translateY(-50%);
  color: #e6e6e6;
  text-align: center;
  line-height: 0.666667rem;
}

.push-course-dialog .container .main > a {
  color: #d1c1b4;
  line-height: 1;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
     -moz-box-align: center;
          align-items: center;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
     -moz-box-pack: center;
          justify-content: center;
  margin-top: 0.4rem;
}

[data-dpr="1"] .push-course-dialog .container .main > a {
  font-size: 12px;
}

[data-dpr="2"] .push-course-dialog .container .main > a {
  font-size: 24px;
}

[data-dpr="3"] .push-course-dialog .container .main > a {
  font-size: 36px;
}

.push-course-dialog .container .main > a > .how-to-push {
  display: inline-block;
  background-image: url(data:image/png;base64,);
  -webkit-background-size: contain;
          background-size: contain;
  background-repeat: no-repeat;
  margin-right: 0.106667rem;
}

[data-dpr="1"] .push-course-dialog .container .main > a > .how-to-push {
  width: 12px;
  height: 12px;
}

[data-dpr="2"] .push-course-dialog .container .main > a > .how-to-push {
  width: 24px;
  height: 24px;
}

[data-dpr="3"] .push-course-dialog .container .main > a > .how-to-push {
  width: 36px;
  height: 36px;
}

.push-course-dialog .container .operation {
  border-top: 1px solid #f0f0f0;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
     -moz-box-pack: center;
          justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
     -moz-box-align: center;
          align-items: center;
}

[data-dpr="1"] .push-course-dialog .container .operation {
  font-size: 15px;
}

[data-dpr="2"] .push-course-dialog .container .operation {
  font-size: 30px;
}

[data-dpr="3"] .push-course-dialog .container .operation {
  font-size: 45px;
}

.push-course-dialog .container .operation .cancel-btn,
.push-course-dialog .container .operation .confirm-btn {
  height: 1.2rem;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
     -moz-box-pack: center;
          justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
     -moz-box-align: center;
          align-items: center;
  -webkit-box-flex: 1;
  -webkit-flex-grow: 1;
     -moz-box-flex: 1;
          flex-grow: 1;
  cursor: pointer;
  -webkit-flex-basis: 2.666667rem;
          flex-basis: 2.666667rem;
}

.push-course-dialog .container .operation .confirm-btn {
  border-left: 1px solid #f0f0f0;
