.address-selector {
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.address-selector div {
  height: 100%;
}

.address-selector .text {
  color: #999;
  height: 100%;
  text-align: right;
}

[data-dpr="1"] .address-selector .text {
  font-size: 13px;
  line-height: 25px;
}

[data-dpr="2"] .address-selector .text {
  font-size: 26px;
  line-height: 50px;
}

[data-dpr="3"] .address-selector .text {
  font-size: 39px;
  line-height: 75px;
}

.ql-s-picker-popup-mask {
  margin: 0 auto;
}

[data-dpr="1"] .ql-s-picker-popup-mask {
  max-width: 640px;
  min-width: 320px;
}

[data-dpr="2"] .ql-s-picker-popup-mask {
  max-width: 1280px;
  min-width: 640px;
}

[data-dpr="3"] .ql-s-picker-popup-mask {
  max-width: 1920px;
  min-width: 960px;
}

.ql-s-picker-popup-wrap {
  margin: 0 auto;
}

[data-dpr="1"] .ql-s-picker-popup-wrap {
  max-width: 640px;
  min-width: 320px;
}

[data-dpr="2"] .ql-s-picker-popup-wrap {
  max-width: 1280px;
  min-width: 640px;
}

[data-dpr="3"] .ql-s-picker-popup-wrap {
  max-width: 1920px;
  min-width: 960px;
}