.coral-tab-bar {
  position: absolute;
  width: 100%;
  bottom: 0;
  overflow: hidden;
  font-size: 0;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  -webkit-box-pack: center;
     -moz-box-pack: center;
          justify-content: center;
  -webkit-justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
     -moz-box-align: center;
          align-items: center;
  z-index: 200;
  background-color: #fff;
  border-top: 1px solid #d5d5d5;
}

[data-dpr="1"] .coral-tab-bar {
  height: 50px;
  max-width: 640px;
  min-width: 320px;
}

[data-dpr="2"] .coral-tab-bar {
  height: 100px;
  max-width: 1280px;
  min-width: 640px;
}

[data-dpr="3"] .coral-tab-bar {
  height: 150px;
  max-width: 1920px;
  min-width: 960px;
}

.coral-tab-bar .tab-item {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
     -moz-box-flex: 1;
          flex: 1;
  cursor: pointer;
  width: 100%;
  height: 100%;
  position: relative;
  text-align: center;
  line-height: normal;
}

[data-dpr="1"] .coral-tab-bar .tab-item .tab-icon {
  width: 21px;
  height: 21px;
}

[data-dpr="2"] .coral-tab-bar .tab-item .tab-icon {
  width: 42px;
  height: 42px;
}

[data-dpr="3"] .coral-tab-bar .tab-item .tab-icon {
  width: 63px;
  height: 63px;
}

.coral-tab-bar .tab-item .tab-title {
  color: #a89e9e;
}

[data-dpr="1"] .coral-tab-bar .tab-item .tab-title {
  font-size: 11px;
}

[data-dpr="2"] .coral-tab-bar .tab-item .tab-title {
  font-size: 22px;
}

[data-dpr="3"] .coral-tab-bar .tab-item .tab-title {
  font-size: 33px;
}

.coral-tab-bar .tab-item.coral-shop:before {
  content: '';
  display: block;
  background: url(data:image/png;base64,) center center no-repeat;
  -webkit-background-size: auto 100%;
          background-size: auto 100%;
  margin: 0.133333rem auto 0.053333rem;
}

[data-dpr="1"] .coral-tab-bar .tab-item.coral-shop:before {
  width: 22px;
  height: 22px;
}

[data-dpr="2"] .coral-tab-bar .tab-item.coral-shop:before {
  width: 44px;
  height: 44px;
}

[data-dpr="3"] .coral-tab-bar .tab-item.coral-shop:before {
  width: 66px;
  height: 66px;
}

.coral-tab-bar .tab-item.coral-bought:before {
  content: '';
  display: block;
  background: url(data:image/png;base64,) center center no-repeat;
