}

[data-dpr="2"] .coral-tab-bar .tab-item.coral-bought:before {
  width: 44px;
  height: 44px;
}

[data-dpr="3"] .coral-tab-bar .tab-item.coral-bought:before {
  width: 66px;
  height: 66px;
}

.coral-tab-bar .tab-item.active.coral-shop:before {
  background-image: url(data:image/png;base64,);
