.intro-group-bar-wrap .intro-group-bar {
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
     -moz-box-align: center;
          align-items: center;
  background: #fff;
  border: 0.013333rem solid #eeeeee;
  -webkit-border-radius: 0.106667rem;
          border-radius: 0.106667rem;
  padding: 0.133333rem 0.266667rem 0.133333rem 0.133333rem;
}

.intro-group-bar-wrap .intro-group-bar .head-img {
  width: 1.013333rem;
  height: 1.013333rem;
  margin-right: 0.266667rem;
}

.intro-group-bar-wrap .intro-group-bar .head-img img {
  width: 100%;
  height: 100%;
}

.intro-group-bar-wrap .intro-group-bar .content {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
     -moz-box-flex: 1;
          flex: 1;
}

.intro-group-bar-wrap .intro-group-bar .content .title {
  font-weight: 500;
  color: #333333;
  line-height: 0.6rem;
}

[data-dpr="1"] .intro-group-bar-wrap .intro-group-bar .content .title {
  font-size: 16px;
}

[data-dpr="2"] .intro-group-bar-wrap .intro-group-bar .content .title {
  font-size: 32px;
}

[data-dpr="3"] .intro-group-bar-wrap .intro-group-bar .content .title {
  font-size: 48px;
}

.intro-group-bar-wrap .intro-group-bar .btn-wrap .btn {
  height: 0.666667rem;
  background: transparent;
  color: #F73657;
  font-weight: bold;
  border: 0 none;
  margin-right: 0.4rem;
}

[data-dpr="1"] .intro-group-bar-wrap .intro-group-bar .btn-wrap .btn {
  font-size: 12px;
}

[data-dpr="2"] .intro-group-bar-wrap .intro-group-bar .btn-wrap .btn {
  font-size: 24px;
}

[data-dpr="3"] .intro-group-bar-wrap .intro-group-bar .btn-wrap .btn {
  font-size: 36px;
}

.intro-group-bar-wrap .intro-group-bar .close-btn {
  width: 0.4rem;
}

.intro-group-bar-wrap .intro-group-bar .close-btn img {
  width: 100%;
}