.roll-down-box .roll-down-inner {
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
     -moz-box-align: center;
          align-items: center;
  -webkit-transition: top 30s linear;
  -moz-transition: top 30s linear;
  transition: top 30s linear;
}

.roll-down-box .roll-down-inner.scroll {
  position: fixed;
  width: 100%;
  left: 50%;
  top: 0;
  -webkit-box-sizing: border-box;
     -moz-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-transform: translateX(-50%);
     -moz-transform: translateX(-50%);
          transform: translateX(-50%);
  z-index: 11;
  background: #fff;
  -webkit-transition: top 0.3s linear;
  -moz-transition: top 0.3s linear;
  transition: top 0.3s linear;
}

[data-dpr="1"] .roll-down-box .roll-down-inner.scroll {
  max-width: 640px;
  min-width: 320px;
}

[data-dpr="2"] .roll-down-box .roll-down-inner.scroll {
  max-width: 1280px;
  min-width: 640px;
}

[data-dpr="3"] .roll-down-box .roll-down-inner.scroll {
  max-width: 1920px;
  min-width: 960px;
}

.roll-down-box .roll-down-inner.scroll.fixed {
  top: -100%;
}

.roll-down-box .roll-down-inner.scroll.down {
  top: 0;
}