.ql-s-picker-bar {
  min-height: 0.266667rem;
}

.ql-s-picker-box {
  min-height: 0.266667rem;
}

.ql-s-picker-box .ql-s-picker-popup-mask {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.4);
  -webkit-transition: all .3s;
  -moz-transition: all .3s;
  transition: all .3s;
}

.ql-s-picker-box .ql-s-picker-popup-mask.hide {
  z-index: -1;
  background-color: rgba(0, 0, 0, 0);
}

.ql-s-picker-box .ql-s-picker-popup-wrap {
  position: fixed;
  left: 0;
  right: 0;
  bottom: -100%;
  z-index: 1000;
  background-color: #fff;
  -webkit-transition: all .3s;
  -moz-transition: all .3s;
  transition: all .3s;
}

.ql-s-picker-box .ql-s-picker-popup-wrap.popup {
  bottom: 0;
}

.ql-s-picker-box .ql-s-picker-popup-wrap .ql-s-picker-popup-header {
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: flex;
  border-bottom: 0.013333rem solid #ddd;
}

.ql-s-picker-box .ql-s-picker-popup-wrap .ql-s-picker-popup-header .ql-s-picker-popup-item {
  padding: 0.133333rem 0.4rem;
  height: 0.8rem;
  font-size: 0.453333rem;
  line-height: 0.8rem;
  color: #000;
  text-align: center;
}

.ql-s-picker-box .ql-s-picker-popup-wrap .ql-s-picker-popup-header .ql-s-picker-popup-item.ql-s-header-title {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
     -moz-box-flex: 1;
          flex: 1;
}

.ql-s-picker-box .ql-s-picker-popup-wrap .ql-s-picker-popup-header .ql-s-picker-popup-item.ql-s-header-left {
  color: #999999;
}

.ql-s-picker-box .ql-s-picker-popup-wrap .ql-s-picker-popup-header .ql-s-picker-popup-item.ql-s-header-right {
  color: #F73657;
}